import { createStore } from 'vuex'
import axios from 'axios'
import { toRaw } from 'vue'
//import AOS from 'aos';
/*eslint-disable */


export default createStore({
  state: {
    darkStyle: false,
    dark: '#212624',
    darkVirgin: false,
    categories: [''],
    headerBarHeight: null,
    showSorting: false, //Тумблер сортировки 
    filterShow: false, //Тумблер фильтра
    cartShow: false, //Тумблер корзины
    confirm: false,
    user_valid: 0,
    user: {
      firstName: null,
      lastName: null,
      number: null,
      email: null,
      delivery: 'np',
      city: null,
      depart: null,
      street: null,
      pay: 'cash',
      comment: null,
      feedback: false,
      feedbackMethod: null,
    },
    
    error_confrim: {
      firstName: true,
      lastName: true,
      number: true,
      email: true,
      city: true,
      depart: true,
      street: true,
    },
    filter: {
      color: [],
      brand: [],
      size: [],
      material: [],
      filler: [],
      robe: [],
      robe_category: [],
      rug: [],
      towel: [],
      pillow_case: [],
      duvet_size: [],
      sheet_size: [],
    },
    category: '',
    products: [],//Динамический список товаров , который будет меняться в зависимости от фильтра
    productsDB: [], //Статический список всех товаров 
    cart: [],
    cartTotal: 0,
    cartRefresh: false,
    columnsGrid: 0,
    columnsCategory: 0,
    commit_triger: false,
    //class
    darkClass: {
      background: '#212624',
      color: 'white', 
    },
    url: '',
    drop_menu_header: false,
    heightFooter: 0,
  },
  getters: {
    footerHeight(state) {
      return state.heightFooter
    },

    dark(state) {
      return state.darkStyle
    },
    categories(state) {
      return state.categories
    },
    productsDB(state) {
      return state.productsDB
    },
    products(state) {
      return state.products
    },
    cart(state){
      return state.cart
    },
    cartLength(state) {
      return state.cart.length
    },
    cartTotalPay(state) {
      var total = 0
      if (state.cart != null) {
        for(var x of state.cart) {
          total = total + (parseInt(x.price) * x.total) 
        }
      }
      return total 
    },
    cartTotal(state) {
      var total = 0
      if (state.cart != null) {
        for(var x of state.cart) { 
          total += x.total
        }
      }
      state.cartTotal = total
      return state.cartTotal
    }
  },
  mutations: {
    
    /*eslint-disable */
    setFooterHeight(state, height) {
      state.heightFooter = height;
      console.log(state.heightFooter);
    },
    DROP_MENU_HEADER(state) {
      state.drop_menu_header = !state.drop_menu_header
    },
    VALID(state) {
      state.user_valid == 0 ? state.user_valid = 1 : true
    },
    TAKE_PRODUCTS_FROM_DB(state,products_db) {
      state.products = products_db 
      state.productsDB = products_db 
      state.products_category_static = products_db
    },
    FILTER(state) {
      if (state.filter.color.length > 0 || state.filter.brand.length > 0 || state.filter.size.length > 0 ||
        state.filter.material.length > 0 || state.filter.filler.length > 0 || state.filter.rug.length > 0 ||
        state.filter.robe.length > 0 || state.filter.towel.length > 0 || state.filter.pillow_case.length > 0
        || state.filter.duvet_size.length > 0 || state.filter.sheet_size.length > 0 || state.filter.robe_category.length > 0 ) { //Если юзер ничего не выбрал в фильтре
        let productsAfterFilter = []
        for (let product of state.productsDB) {//Пробрасываем продукт по всем фильтрам и если тру = добавляем в новый отфильтрованный массив
          if (state.filter.color.length > 0) {//Фильтр цвет
            if (state.filter.color.find(f => f == product.color)) true
            else {continue}
           }
           if (state.filter.brand.length > 0) {//Фильтр на бренд
            //console.log(state.filter.brand.find(f => f == product.brand));
            if (state.filter.brand.find(f => f == product.brand)) true
            else { continue}
           }
           if (state.filter.size.length > 0) { //Фильтр на размер
            if (state.filter.size.find(f => f == product.size)) true
            else {continue}
           }  
           if (state.filter.material.length > 0) { 
            if (state.filter.material.find(f => f == product.material)) true
            else {continue}
           }  
           if (state.filter.filler.length > 0) { 
            if (state.filter.filler.find(f => f == product.filler)) true
            else {continue}
          } 
          if (state.filter.rug.length > 0) { 
            if (state.filter.rug.find(f => f == product.rug)) true
            else {continue}
          } 
          if (state.filter.robe.length > 0) { 
            if (state.filter.robe.find(f => f == product.robe)) true
            else {continue}
          } 
          if (state.filter.towel.length > 0) { 
            if (state.filter.towel.find(f => f == product.towel)) true
            else {continue}
          } 
          if (state.filter.pillow_case.length > 0) { 
            if (state.filter.pillow_case.find(f => f == product.pillow_case)) true
            else {continue}
          } 
          if (state.filter.duvet_size.length > 0) { 
            if (state.filter.duvet_size.find(f => f == product.duvet_size)) true
            else {continue}
          } 
          if (state.filter.sheet_size.length > 0) { 
            if (state.filter.sheet_size.find(f => f == product.sheet_size)) true
            else {continue}
          } 
          if (state.filter.robe_category.length > 0) { 
            if (state.filter.robe_category.find(f => f == product.robe_category)) true
            else {continue}
          } 
           productsAfterFilter.push(product) //Если все условия тру, продукт проходить фльтрацию 
        }
        state.products = productsAfterFilter
         //По итогу изменяем отображаемый список товаров
      }
      else {state.products = state.productsDB //Если юзер ничего ничего не выбрал или отжал обратно, список товаров возвращается в неотфилтрованное положение 
        for (let product of state.productsDB) {
          product.filtered = 0
        }
      }
    },
    FILTER_REFRESH(state) {//filter
      for (let product of state.productsDB) {
        product.filtered = 0
      }
      state.products = state.productsDB;
    },
    REFRESH(state) {
      state.products = state.productsDB;
    },
    FILTER_SHOW(state) {//filter
      state.filterShow ? state.filterShow = false : state.filterShow = true},

    FILTER_COLOR(state, colorSelect) {//filter
      state.filter.color = colorSelect },
    FILTER_BRAND(state, brandSelect) {//filter
      state.filter.brand = brandSelect},
    FILTER_SIZE(state, sizedSelect) {//filter
      state.filter.size = sizedSelect},
    FILTER_MATERIAL(state, materialSelect) {
      state.filter.material = materialSelect},
    FILTER_FILLER(state, fillerSelect) {
      state.filter.filler = fillerSelect},
    FILTER_RUG(state, rugSelect) {
      state.filter.rug = rugSelect},
    FILTER_ROBE(state, robeSelect) {
      state.filter.robe = robeSelect},
    FILTER_ROBE_CATEGORY(state, robeCategorySelect) {
      state.filter.robe_category = robeCategorySelect},
    FILTER_TOWEL(state, towelSelect) {
      state.filter.towel = towelSelect},
    FILTER_PILLOW_CASE(state, pillowCaseSelect) {
      state.filter.pillow_case = pillowCaseSelect},
    FILTER_DUVET_SIZE(state, duvetSizeSelect) {
      state.filter.duvet_size = duvetSizeSelect},
    FILTER_SHEET_SIZE(state, sheetSizeSelect) {
      state.filter.sheet_size = sheetSizeSelect},

    /////////////FILTER_END

    STANDART(state) { //sorting
      state.products.sort((a, b) => parseInt(a.id) > parseInt(b.id) ? 1 : -1)
      state.commit_triger = !state.commit_triger
    },
    PRICE(state) { //sorting
      state.products.sort((a, b) => parseInt(a.price) > parseInt(b.price) ? 1 : -1)
      state.commit_triger = !state.commit_triger
    },
    PRICE_REVERSE(state) { //sorting
      state.products.sort((a, b) => parseInt(a.price) < parseInt(b.price) ? 1 : -1)
      state.commit_triger = !state.commit_triger
    },
    ALPH(state) { //sorting
      state.products.sort((a, b) => a.name > b.name ? 1 : -1)
      state.commit_triger = !state.commit_triger
    },
    ALPH_REVERSE(state) { //sorting
      state.products.sort((a, b) => a.name < b.name ? 1 : -1)
      state.commit_triger = !state.commit_triger
    },
    OPEN_SORT(state) { //sorting
      state.showSorting ? state.showSorting = false : state.showSorting = true  
    },
    CLOSE_SORT(state) { //sorting
      state.showSorting = false
    },
    MOUSE_SORT(state) { //sorting
      state.showSorting = true
    },
    MOUSE_SORT_LEAVE(state) { //sorting
      state.showSorting = false
    },
    CART_SHOW(state) {
      state.cartShow = !state.cartShow
    },
    CART_INIT(state) {
      if(localStorage.getItem('cart') == null) return 
      state.cart = [JSON.parse(localStorage.getItem('cart'))].flat(1) 
    },
    CART_ADD(state, product) {
      //localStorage.clear()
      if(localStorage.getItem('cart') == null) {
        localStorage.setItem('cart', JSON.stringify(product)) 
        state.cart.push(toRaw(product))
      }
      if (localStorage.getItem('cart') != null) {
        var local = [JSON.parse(localStorage.getItem('cart'))].flat(1) 
        var cart = []
        for (var x of local) { 
          cart.push(x)
        }
        for(var y of cart) { //Поиск одинаковых 
          if(y.article == toRaw(product.article)) {
            return
          }
        }
        state.cart.push(toRaw(product))
        cart.push(toRaw(product));
        localStorage.setItem('cart', JSON.stringify(cart));
      }
    },
    CART_TOTAL_CHANGE(state) {
      localStorage.setItem('cart', JSON.stringify(state.cart));
    },
    CART_REMOVE(state, product) {
      state.cart[state.cart.indexOf(product)].total = 1
      state.cart.splice(state.cart.indexOf(product), 1);
      localStorage.setItem('cart', JSON.stringify(state.cart));
    },
    CART_TOTAL_ADD(state, product) {
      state.cart[state.cart.indexOf(product)].total ++
    },
    CART_TOTAL_REMOVE(state, product) {
      if (state.cart[state.cart.indexOf(product)].total > 1) {
        state.cart[state.cart.indexOf(product)].total--
      }
    },
    CART_REFRESH(state) {
      state.cart = []
      localStorage.removeItem('cart')
    },
    DARK(state) {
      state.darkStyle = !state.darkStyle
      state.darkVirgin = true
      localStorage.setItem('theme', state.darkStyle)
    },
    VIRGIN(state) {
      state.darkVirgin = false
    },
    DARK_INIT(state) {
      if(localStorage.getItem('theme') == 'false') state.darkStyle = false
      if(localStorage.getItem('theme') == 'true') state.darkStyle = true
    },
    TAKE_CATEGORIES(state, categories) {
      state.categories = categories
    },
    SELECT_CATEGORY(state, categoryTranslite) { 
      for (let x in state.categories) {
        if (state.categories[x].url == categoryTranslite) { // ищем и определяем категорию из транслита
          state.category = state.categories[x].category
        }
      }
      
    },
    CATEGORY_FILTER(state) {
      let products = state.products.filter(p => p.category == state.category)
      state.products = products
    },
    GET_GRID_COLUMNS(state, columns) {
      state.columnsGrid = columns
    },
    GET_CATEGORY_COLUMNS(state, columns) {
      state.columnsCategory = columns
    },
    DESTROY_PRODUCTS(state) {
      state.products = []
      console.log(state.products);
    }
  },
  actions: {
    async GET_PRODUCTS({state, commit}) {
      try {
        const response = await axios.get(`${state.url}/api/products/?category=${state.category}`);
        const products_db = response.data.map(product => {
          return {
            ...product,
            showPhoto: false,
            showPrice: false,
            total: 1,
          }
        })
        commit('TAKE_PRODUCTS_FROM_DB', products_db) // передаем данные в мутацию
      }
      catch(error) { 
        console.log("GET_PRODUCTS AXIOS ERROR")
        console.log(error)
      }
    },
    async GET_PRODUCT({commit}, article) {
      try {
        const response = await axios.get(`${state.url}/api/products/?article=${article}`)

      }
      catch {

      }
    },
    async GET_CATEGORIES({state, commit}) {
      try {
        const response = await axios.get(`${state.url}/api/categories/`)
        const categories = response.data
        commit('TAKE_CATEGORIES', categories)
      }
      catch (error) {
        console.log(error)
      }
    }
  },
  modules: {
  }
})

