<template>
  <div 
  :class="[
      this.$store.state.darkVirgin ? {
        dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
      } : {
        darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
      },
      {uiAnim : !this.$store.state.filterShow}
    ]"
     class="footer">
    <div class="footerMain">
        <div class="leftFooter">
          <div class="socials">
            <div class="social" v-for="social in socials" :key="social">
              <a
              :href="social.url"
              :class="{darkSimbol : this.$store.state.darkStyle}"
              >{{ social.name }}</a>
            </div>
          </div>
        </div>
        <div class="centerFooter">
  
        </div>
        <div class="rightFooter">
          <div class="pages">
            <div class="phone">
              <a :class="{darkSimbol : this.$store.state.darkStyle}" href="tel:+380678523711">+380 67-852-37-11</a>
          </div>
            <div class="politic">
              <router-link :to="{path: '/politic'}"
              :class="{darkSimbol : this.$store.state.darkStyle}">
              політика
              </router-link>
            </div>
            <div class="return">
              <router-link :to="{path: '/return'}"
              :class="{darkSimbol : this.$store.state.darkStyle}">
              повернення
              </router-link>
            </div>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex';
import axios from 'axios';

export default {
  data() {
    return {
      socials: []
    }
  },
  mounted() {
    this.fetchSocials();
    const footerElement = document.querySelector('.footer')
    if (footerElement) {
      const height = footerElement.clientHeight;
      this.setFooterHeight(height);
    }
  },
  methods: {
    fetchSocials() {
      axios.get(`${this.$store.state.url}/api/info/`)
        .then(response => {
          this.socials = response.data;
        })
        .catch(error => {
          console.error(error);
        });
    },
    ...mapMutations(['setFooterHeight']),
  }
}
</script>

<style lang="sass" scoped>
    .footer 
        border-top: 1px solid #e0e1e0
        width: 100%
        height: 100px
        display: flex
        justify-content: center
        padding: 20px 50px
        align-items: center
        position: absolute 
        bottom: 0px 
        @media (max-width: 550px)
          padding: 10px 20px
        .footerMain 
            height: 100%
            width: 100%
            display: flex
            justify-content: space-between
            position: relative
            .leftFooter
              height: 100%
              flex-direction: column
              .socials
                height: 100%
                display: flex
                align-items: center
              
                @media (max-width: 550px)
                  flex-direction: column
                  align-items: flex-start
                  text-align: center
                .social
                  margin-right: 15px
                  letter-spacing: 1px
                  display: flex
                  a
                    text-decoration: none 
                    color: grey
                    &:hover
                      text-decoration: underline
            .rightFooter
              height: 100%
              .pages
                height: 100%
                align-items: center
                display: flex
                @media (max-width: 550px)
                  flex-direction: column
                  align-items: flex-end
                  justify-content: center
                .phone
                  margin: auto
                  align-items: center
                  display: flex
                  position: relative
                  top: 2px
                  a 
                    text-decoration: none
                    font-size: 13px
                    &:hover
                      text-decoration: underline
                .return, .politic
                  margin-left: 20px
                  text-decoration: none
                  a
                    text-decoration: none 
                    color: grey
                    font-size: 16px
                    &:hover
                      text-decoration: underline
            .centerFooter
              position: absolute
</style>