<script>
/*eslint-disable */
import store from '@/store'
import CartVue from '@/components/CartVue.vue'
import MoonSun from './MoonSun.vue'
import axios from 'axios';
import { mapState } from 'vuex';

export default {
    components: {
        CartVue, 
        MoonSun
    },
    mounted() {
        window.scrollTo(0, 0);
        //this.fetchIconImage();
    },
    beforeMount() {
        
    },
    data() {
        return {
            price: 0,
            scroll: null,
            theme: "Light",
            white: {
                color: 'white'
            },
            loaded: false,
            isOpen: true,
        }
    },
    methods: {
        openCart() {
            store.commit("CART_SHOW")
            store.commit("VIRGIN")
        },
        scrolling() {
            //this.scroll = window.scrollY
        },
        async fetchIconImage() {
            try {
                const response = await axios.get(`${this.$store.state.url}/api/icons/`);
                const icons = response.data;
                const targetIcon = icons.find(icon => icon.filename === 'gear');

                if (targetIcon) {
                this.settings = targetIcon.image
                }
            } catch (error) {
                console.error('Ошибка при получении данных:', error);
            }
        },
        toggleSettings() {
            store.commit('DROP_MENU_HEADER')
        },
    },
    computed: {
        cartTotal() {
            return store.getters.cartTotalPay
        },
        
    },
    watch: {
        '$store.state.darkStyle': function() {
            if(this.$store.state.darkStyle == false) this.theme = "Light";
            if(this.$store.state.darkStyle == true) this.theme = "Dark"
        },
    }
}
</script>

