<template>
  <div :class="[
    this.$store.state.darkVirgin ? {
      dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
    } : {
      darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
    }
  ]" 
  :style="{ 'padding-bottom': footerHeight + 25 + `px` }"
  class="main">  
    <HeaderBar></HeaderBar>
    <router-view v-slot="{Component, route}">
      <transition :name="route.meta.transition" mode="out-in">
        <component :is="Component" :key="route.path"/>
      </transition>
    </router-view>
    <FooterVue></FooterVue>
  </div>
</template>

<script>
/*eslint-disable */
import MainPage from './components/MainPage.vue'
import HeaderBar from '@/components/HeaderBar.vue'
import FooterVue from '@/components/FooterVue.vue'
import store from '@/store'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'App',
  components: {
    MainPage,
    HeaderBar,
    FooterVue,
  },
  data() {
    return{ 
      scroll: 0,
      width: 0,
      columnsProduct: 0,
      
    }
  },
  beforeCreate() {
    
  },
  async created() {
    this.columnsProduct = this.getColumns()
    this.columnsCategory = this.getColumnsCategory()
    store.commit('GET_GRID_COLUMNS', this.columnsProduct)
    store.commit('GET_CATEGORY_COLUMNS', this.columnsCategory)
  },
  
  mounted() {
    
  },

  methods: {
    scrolling() {
  
    },
    ...mapActions([
      'GET_CATEGORIES'
    ]),
    getColumns() {
      const screenWidth = window.innerWidth;
      if (screenWidth >= 1500) {
        return 4;
      } else if (screenWidth >= 1024) {
        return 3;
      } else if (screenWidth >= 650) {
        return 2;
      } else {
        return 1;
      }
    },
    getColumnsCategory() {
      const screenWidth = window.innerWidth;
      if (screenWidth >= 1300) {
        return 4;
      } else if (screenWidth >= 900) {
        return 3;
      } else if (screenWidth >= 550) {
        return 2;
      } else {
        return 1;
      }
    },
  },
  computed: {
    ...mapGetters(['footerHeight']),
  },
  watch: {
    
  }
}
</script>

