<template>
  <div>
  <div :key="$route.path" 
  
  class="grid">
    <div class="gridBanner" :class="{darkSimbol : this.$store.state.darkStyle}">
       <span :class="{darkSimbol : this.$store.state.darkStyle}">{{ this.$store.state.category }}</span>
    </div>
    <div 
    :class="[
      this.$store.state.darkVirgin ? {
        dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
      } : {
        darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
      },
      {uiAnim : !this.$store.state.filterShow}
    ]"
      class="uiGrid">
        <div class="leftGrid">
        
        </div>
        <div class="rightGrid">
            <div @mouseleave="mouseSortLeave" @mouseenter="mouseSort" class="sort" 
            :class="{sortLeft : this.$store.state.filterShow, sortRight : !this.$store.state.filterShow }">
              <p :class="{darkSimbol : this.$store.state.darkStyle}" class="openSort" @click="openSort">Сорт.</p>
              <transition name="sorting"><SortingVue v-show="$store.state.showSorting"></SortingVue></transition>
            </div>
            <div @click="filter" class="filter"
            :class="{filterRight : !this.$store.state.filterShow }">
               <span :class="{darkSimbol : this.$store.state.darkStyle}">Фільтр</span>
             </div>
         </div>
     </div>
     <div class="loading" v-if="!load || this.$store.state.products.length == 0">
       <div class="load-svg">
         <img :src="this.$store.state.darkStyle ? 'loadDark.svg' : 'load.svg'" alt="Loading GIF">
         <div v-if="this.$store.state.products.length == 0" class="apologize"
         >
           <span
           :class="{darkSimbol : this.$store.state.darkStyle}"
           >Приносимо вибачення за незручності</span><br>
           <span
           :class="{darkSimbol : this.$store.state.darkStyle}"
           >За цим запитом не виявлено жодного товару</span>
           <span></span>
        </div>
      </div>
    </div>
    <div v-if="load" class="gridContent">
        <ProductVue  v-for="(product, index) in products" :key="product"

        v-bind:product="product"
        v-bind:category="category"
        :data-aos="index % 2 == 0 ? 'fade-up' : 'fade-down'"
        :data-aos-delay="(index % this.$store.state.columnsGrid) * 100"
        :data-aos-duration="300"
        data-aos-reverse="true"
        data-aos-mirror="true"
        
        ></ProductVue>
    </div>
  </div>
    <transition name="filter-fade">
      <FilterVue
      v-show="$store.state.filterShow"
      ></FilterVue>
    </transition>
  </div>
</template>

<script>
/*eslint-disable */
import store from '@/store'
import ProductVue from '@/components/ProductVue.vue'
import SortingVue from './SortingVue.vue'
import MoonSun from './MoonSun.vue'
import { mapGetters, mapActions } from 'vuex'
import FilterVue from './FilterVue.vue'
import AOS from 'aos';
import 'aos/dist/aos.css';

export default {
  data() {
    return {
      load: false,
    }
  },
    created() { //Получаем все категории,находим выбранную юзером категорию, отаврляем категорию в vuex,где action сделает через нее запрос,получаем товары
      store.commit("VIRGIN")
      if (this.$store.state.filterShow) {
      store.commit("FILTER_SHOW")
    }
      this.$store.dispatch('GET_CATEGORIES').then(() => {
        this.$store.commit('SELECT_CATEGORY', this.$route.params.category)
        //this.$store.dispatch('GET_PRODUCTS')
        this.$store.dispatch('GET_PRODUCTS').then(() => {
          this.load = true
        }) 
      })
    },

  mounted() {
    window.scrollTo(0, 0);
    window.onload = function() {
      AOS.init();
      AOS.refresh();
    }
    window.addEventListener("scroll", function() {
      try {
        if (this.window.scrollY > document.querySelector('.uiGrid').getBoundingClientRect().top) {
          document.querySelector('.uiGrid').style.top=document.querySelector('.headerWrap').offsetHeight  + 'px'
        }
        if (document.querySelector('.uiGrid').getBoundingClientRect().top < 77) {
          document.querySelector('.uiGrid').style.borderTop='0px'
        }
        if (document.querySelector('.uiGrid').getBoundingClientRect().top > 77) {
          document.querySelector('.uiGrid').style.borderTop='1px solid #e0e1e0'
        }
      } catch {true}
    })  
  },
  watch: {
  '$store.state.products': function() {
      this.load = false
      setTimeout(() => this.load = true, 100)
    },
  '$store.state.commit_triger': function() {
    this.load = false
    setTimeout(() => this.load = true, 100)
  },
  },
  props: {
    category: {
      type: String
    }
  },
  components: {
    FilterVue,
    ProductVue,
    SortingVue,
    MoonSun,
  },
  methods: {

    scrollToTop() {
      window.scrollTo(0, 0);
    },
    filter() {
      store.commit("FILTER_SHOW")
      store.commit("VIRGIN")
    },
    openSort() {
      store.commit('OPEN_SORT')
      store.commit("VIRGIN")
    },
    mouseSort() {
      store.commit('MOUSE_SORT')
      store.commit("VIRGIN")
    },
    mouseSortLeave() {
      store.commit('MOUSE_SORT_LEAVE')
    },
  },
  computed: {
    ...mapGetters([
      'productsDB', 
      'products'
    ]),
    ...mapActions([
      'GET_PRODUCTS', 
      'GET_CATEGORIES'
    ]),
  },

}
</script>

<style lang="sass" scoped>
    .grid
        z-index: 9
        position: relative
        height: auto
        .gridBanner 
          height: 70px
          display: flex
          align-items: center
          white-space: nowrap
          overflow: hidden
          justify-content: center
          align-items: center
          letter-spacing: 3px
          text-transform: lowercase
          font-size: 16px
          span
            position: relative
            right: 2px
            color: grey 
        .uiGrid
          border-top: 1px solid #e0e1e0
          border-bottom: 1px solid #e0e1e0
          border-right: 1px solid #e0e1e0
          border-left: 1px solid #e0e1e0
          height: 70px
          width: 100%
          display: flex
          justify-content: space-between
          flex-wrap: nowrap
          position: sticky
          z-index: 6
          .leftGrid
            height: 100%
            width: atuo
            @media screen and (max-width: 650px)
              display: none
          .rightGrid
            height: 100%
            display: flex
            @media screen and (max-width: 650px)
              width: 100%
            .sort
              border-left: 1px solid #e0e1e0
              width: 250px
              height: 100%
              display: flex
              flex-direction: column
              position: relative
              z-index: 5
            .filter
              border-left: 1px solid #e0e1e0
              width: 230px
              height: 100%
            .filter span, .sort span, .sort p
              color: grey
              font-family: FuturaBook
              text-transform: uppercase
              letter-spacing: 2px
            .sort p
              width: 100%
              height: 100%
              display: flex
              justify-content: center
              align-items: center
            .filter, .sort
              display: flex
              justify-content: center
              align-items: center
              cursor: pointer
              @media screen and (max-width: 650px)
                width: 50%
        .loading
          height: 500px
          width: 100%
          display: flex
          justify-content: center
          
          align-items: flex-start
          .load-svg
            display: flex
            flex-direction: column
            img
              transform: scale(1)
            .apologize
              position: relative
              font-size: 12px
              text-align: center
              letter-spacing: 2px
        .gridContent
          width: 100%
          padding: 40px
          display: grid
          grid-template-columns: 1fr 1fr 1fr 1fr
          column-gap: 2%
          justify-content: space-around
          grid-row-gap: 60px
          @media screen and (max-width: 1500px)
            padding: 40px 20px 20px 20px
            grid-template-columns: 1fr 1fr 1fr
          @media screen and (max-width: 1024px)
            grid-template-columns: 1fr 1fr 
          @media (max-width: 650px)
            grid-template-columns: 1fr 
          @media screen and (max-width: 1921px)
            padding: 40px 25px
          @media screen and (max-width: 650px)
            padding: 30px 15px
          @media (max-width: 500px)
            grid-row-gap: 35px

@keyframes marquee 
  0%  
    transform: translateX(0%) 
  100% 
    transform: translateX(100%) 


.sorting-enter-active,
.sorting-leave-active
  transition: all 0.6s ease
  
.sorting-enter-from,
.sorting-leave-to
  height: 0px !important
  position: absolute
  z-index: -5
  top: 45px
  opacity: 0 !important
  border-top: 0px solid
.sorting-enter-to,
.sorting-leave-from
  height: 260px !important
  opacity: 1 !important
  top: 68px

@media (min-width: 720px)
  .sortLeft
    animation: move-left 2s forwards
  .sortRight
    animation: move-right 0.7s forwards

@keyframes move-left  
  0%
    transform: translateX(0px)
  100%
    transform: translateX(-250px)

@keyframes move-right 
  0%
    transform: translateX(-250px)
  100%
    transform: translateX(0px)

.filterRight
  animation: filter-right 0.6s forwards
@keyframes filter-right 
  0%
    transform: translateX(-200px)
    
  100%
    transform: translateX(0px)

.white 
  background-color: white

.filter-fade-enter-active,
.filter-fade-leave-active
  transition: all 0.3s ease
.filter-fade-enter-from,
.filter-fade-leave-to
  transform: translateX(500px)


@keyframes ui 
  0%
    height: 0px
  100%
    height: 70px
</style>        