<script>
export default {
    computed: {
        cartVuex() {
            return this.$store.getters.cart
        },
        cartTotalPay() {
            return this.$store.getters.cartTotalPay
        },
        cartTotal() {
        return this.$store.getters.cartTotal
        }
    },
    methods: {
        remove(product) {
            this.$store.commit('CART_REMOVE', product)
        },
        handleImageError(event) {
            event.target.src = '/error_img.png';
        },
    },
}
</script>

<template>
  <div class="checkOutCart">
     <p class="h3" :class="{darkSimbol : this.$store.state.darkStyle}">Ваш кошик</p>
     <div class="listCart">
         <div class="listCartInfo">
             <span :class="{darkSimbol : this.$store.state.darkStyle}"
             >Кількість товарів : {{ cartTotal }}</span>
             <span :class="{darkSimbol : this.$store.state.darkStyle}"
             >Загальна сума: {{ cartTotalPay }} грн. </span>
         </div>
         <div v-if="cartVuex.length == 0" class="cart-none">
             <span
             :class="{darkSimbol : this.$store.state.darkStyle}"
             >Ваш кошик порожній!</span>
         </div>
         <transition-group name="list">
         <div v-for="product in cartVuex" :key="product" class="itemCart">
             <div class="itemLeft">
                 <div class="itemPhoto">
                     <router-link style="text-decoration: none" :to="'/product/' + product.article"><img @error="handleImageError" :src="product.images[0].image" alt =""></router-link>
                 </div>
                 <div class="itemName">
                     <span :class="{darkSimbol : this.$store.state.darkStyle}">{{ product.name }}</span>
                     <span v-if="product.size" :class="{darkSimbol : this.$store.state.darkStyle}">{{ product.size }}</span>
                     <span v-if="product.robe" :class="{darkSimbol : this.$store.state.darkStyle}">{{ product.robe }}</span>
                 </div>
             </div>
             <div class="itemRight">
                 <div class="itemCount">
                     <span :class="{darkSimbol : this.$store.state.darkStyle}">Кількість : {{ product.total }}</span>
                 </div>
                 <div class="itemPrice">
                     <span :class="{darkSimbol : this.$store.state.darkStyle}"><span :class="{darkSimbol : this.$store.state.darkStyle}" v-if="product.total == 1 ">Ціна товару:</span><span :class="{darkSimbol : this.$store.state.darkStyle}" v-if="product.total > 1">Ціна товарів:</span> {{ product .total * product.price }} грн.</span>
                     <div @click="remove(product)" class="remove" :class="{darkSimbol : this.$store.state.darkStyle}">видалити</div>
                 </div>
            </div>
        </div>
        </transition-group>
    </div>
  </div>
</template>

<style lang="sass" scoped>
    .checkOutCart
        width: 100%

        .h3
            display: flex
            justify-content: center
            font-size: 14px
            color: #131621
            text-transform: uppercase
            font-family: FuturaBook
            letter-spacing: 2px
            margin-bottom: 10px
        .listCart
            display: flex
            flex-direction: column
            .listCartInfo
                width: 100%
                height: 50px
                border: 1px solid #e0e1e0
                border-radius: 5px
                margin-bottom: 10px
                font-size: 11px
                padding: 0px 15px
                letter-spacing: 2px
                display: flex
                justify-content: space-between
                align-items: center
            .cart-none
                width: 100%
                height: 50px
                border: 1px solid red
                border-radius: 5px
                display: flex
                align-items: center
                justify-content: center
                letter-spacing: 2px
                font-size: 12px
            .itemCart
                height: 115px
                width: 100%
                border: 1px solid black
                display: flex
                align-items: center
                padding: 10px 10px 10px 10px
                justify-content: space-between
                margin-bottom: 10px
                border: 1px solid #e0e1e0
                border-radius: 5px
                .itemLeft
                    display: flex
                    align-items: center
                    height: 100%
                    .itemPhoto
                        display: flex
                        align-items: center
                        justify-content: center
                        img
                            aspect-ratio: 0.9
                            width: 80px
                            border: 1px solid #e0e1e0
                    .itemName
                        height: 100%
                        padding: 0px 10px
                        display: flex
                        flex-direction: column
                        span
                            font-size: 12px
                            &:first-child
                                font-size: 13px
                .itemRight
                    display: flex
                    padding-top: 3px
                    height: 100%
                    width: 180px
                    justify-content: space-between
                    font-size: 11px
                    letter-spacing: 0px
                    .itemCount
                    .itemPrice
                        display: flex
                        flex-direction: column
                        justify-content: space-between
                        text-align: end
                        align-items: end
                        padding-bottom: 2px
                        div
                            font-size: 10px
                            letter-spacing: 1px
                            border: 1px solid #e0e1e0
                            padding: 3px 7px
                            cursor: pointer
                            color: grey
                    
.list-move, .list-leave-active
  transition: all 0.4s ease
.list-enter-active
  transition: all 0.4s ease
.list-enter-from , .list-leave-to
  transform: translateX(-1000px)
  opacity: 0
.list-enter-to, .list-leave-from
  transform: translateX(0px)
  opacity: 1

.list-leave-active 
  position: absolute

</style>