<template>
  <div    
  :class="[
      this.$store.state.darkVirgin ? {
        dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
      } : {
        darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
      }
    ]" class="sorting">
    <form action="">
      <label class="label labelAnim">
       <input @change="standart" v-model="input" type="radio" name="radio" value="standart" checked/>
       <span :class="{greySimbol : this.$store.state.darkStyle}">Стандарт </span>
     </label>
     <label class="label labelAnim">
       <input @change="price" v-model="input" type="radio" value="price" name="radio"/>
       <span :class="{greySimbol : this.$store.state.darkStyle}">Ціна, від низ. до вис.</span>
     </label >
     <label class="label labelAnim" >
       <input @change="priceRevers" v-model="input" type="radio" value="priceRevers" name="radio"/>
       <span :class="{greySimbol : this.$store.state.darkStyle}">Ціна, від вис. до низ.</span>
     </label >
     <label class="label labelAnim">
       <input @change="alph" v-model="input" type="radio" value="alph" name="radio"/>
       <span :class="{greySimbol : this.$store.state.darkStyle}">Алфавіт, від А до Я</span>
     </label>
     <label class="label labelAnim">
       <input @change="alphRevers" v-model="input" type="radio" value="alphRevers" name="radio"/>
       <span :class="{greySimbol : this.$store.state.darkStyle}">Алфавіт, від Я до А</span>
     </label>
  
    </form>
  </div>
</template>

<script>
/*eslint-disable */
import store from '@/store'
import { mapActions } from 'vuex'


export default {

  mounted() {
    let label = document.querySelectorAll('.labelAnim')
    let i = 0
    function anim() {
      i++
      if (i <= label.length) {
        label[i - 1].classList.add('labelReverse')
        setTimeout(anim, 200)
      }
    }
    anim()
  },
  data() {
    return {
     input: '',
    }
  },
  methods: {
    standart() {
      store.commit('STANDART')
    },  
    price() {
      store.commit('PRICE')
    },
    priceRevers() {
      store.commit('PRICE_REVERSE')
    },
    alph() {
      store.commit('ALPH')
    },
    alphRevers() {
      store.commit('ALPH_REVERSE')
    }
  },
}
</script>

<style lang="sass" scoped>

$primary-color: #131621
$text-color: #e0e1e0


.sorting   
    position: absolute
    width: 251px
    top: 68px
    left: -1px
    background-color: white
    border: 1px solid #e0e1e0
    min-height: 100px
    padding: 15px 10px
    overflow: hidden
    z-index: 10
    @media screen and (max-width: 650px)
      width: 101%
    @media screen and (max-width: 470px)
      width: 150%
    form
      display: flex
      flex-direction: column
      .label
        font-family: FuturaLight
        font-size: 15px
        letter-spacing: 1px
        cursor: pointer
        display: flex
        cursor: pointer
        font-weight: 500
        position: relative
        overflow: hidden
        margin-bottom: 0.375em
        margin: 5px 0
        animation: label 0.2s ease
        input
          visibility: hidden
          &:checked + span 
            background-color: #e0e1e0
            &:before 
              box-shadow: inset 0 0 0 0.4375em $primary-color
        span 
          display: flex
          align-items: center
          padding: 0.375em 0.75em 0.375em 0.375em
          border-radius: 99em 
          transition: 0.25s ease
          position: relative
          right: 10px
          &:hover 
            background-color: #e0e1e0
          &:before
            display: flex
            flex-shrink: 0
            content: ""
            background-color: #fff
            width: 1.5em
            height: 1.5em
            border-radius: 50%
            margin-right: 0.375em
            transition: 0.25s ease
            box-shadow: inset 0 0 0 0.125em $primary-color
    

.greySimbol
  animation: grey-simbol-change 0.6s forwards
  mix-blend-mode: difference

@keyframes grey-simbol-change 
  from
    color: black
  to
    color: grey
     
@keyframes label
  0%
    opacity: 0
  50%
    opacity: 0
  100%
    opacity: 1
    
.dark 
  border-color: white
              
</style>