<template>
  <div @click="theme" :class="{day : !dark}" class="tdnn">
       <div :class="{sun : !dark}" class="moon">
      </div>
      
  </div>
</template>

<script>
import store from '@/store';
export default {
    data() {
      return {
        
      }
    },
    created() {
      if (localStorage.getItem("theme") == null) localStorage.setItem('theme', this.$store.state.darkStyle)
      else store.commit('DARK_INIT')
    },
    mounted() {
      
    },
    methods: {
      theme() {
        store.commit('DARK')
      }
    },
    computed: {
      dark() {
        return store.getters.dark
      }
    }
}
</script>

<style>
:root {
  --darkbg:#251D29;
  --darkt: #FFD1F7;
  --lightbg: #fff;
  --lightt: #D43370;
  
  --toggleHeight: 16em;
  --toggleWidth: 30em;
  --toggleBtnRadius: 10em;

  --bgColor--night: #423966;
  --mooncolor: white;
  --bgColor--day: #9ee3fb;
}

.tdnn {
  /*change size of toggle with font-size*/
  font-size: 12%;
   position: relative;
    height: 32px;
    width: 60px;
  border-radius: var(--toggleHeight);
  -webkit-transition: all 500ms ease-in-out;
  -o-transition: all 500ms ease-in-out;
  transition: all 500ms ease-in-out;
  background: #2d352f;
  border: 0px solid #e0e1e0;
  cursor: pointer;
}
.day{
  background: rgb(200, 200, 200);
  opacity: 0.8;
}
.moon {
  position: absolute;
  display: block;
  border-radius: 50%;
  -webkit-transition: all 400ms ease-in-out;
  -o-transition: all 400ms ease-in-out;
  transition: all 400ms ease-in-out;
  
  top: 3em;
  left: 6em;
  -webkit-transform: rotate(-75deg);
      -ms-transform: rotate(-75deg);
          transform: rotate(-75deg);
  width: var(--toggleBtnRadius);
  height: var(--toggleBtnRadius);
  -webkit-box-shadow: 
    2em 2.5em 0 0em var(--mooncolor) inset,
    rgba(255, 255, 255, 1) 0em -7em 0 -4.5em,
    rgba(255, 255, 255, 1) 3em 7em 0 -4.5em ,
    rgba(255, 255, 255, 1) 2em 13em 0 -4em,
    rgba(255, 255, 255, 1) 6em 2em 0 -4.1em,
    rgba(255, 255, 255, 1) 8em 8em 0 -4.5em,
    rgba(255, 255, 255, 1) 6em 13em 0 -4.5em,
    rgba(255, 255, 255, 1) -4em 7em 0 -4.5em,
    rgba(255, 255, 255, 1) -1em 10em 0 -4.5em;
          box-shadow: 
    2em 2.5em 0 0em var(--mooncolor) inset,
    rgba(255, 255, 255, 1) 0em -7em 0 -4.5em,
    rgba(255, 255, 255, 1) 3em 7em 0 -4.5em ,
    rgba(255, 255, 255, 1) 2em 13em 0 -4em,
    rgba(255, 255, 255, 1) 6em 2em 0 -4.1em,
    rgba(255, 255, 255, 1) 8em 8em 0 -4.5em,
    rgba(255, 255, 255, 1) 6em 13em 0 -4.5em,
    rgba(255, 255, 255, 1) -4em 7em 0 -4.5em,
    rgba(255, 255, 255, 1) -1em 10em 0 -4.5em;
}
.sun {
  top: 4.5em;
  left: 18em;
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
  width: 7em;
  height: 7em;
  background: #fff;
  -webkit-box-shadow: 3em 3em 0 5em #fff inset,
    0 -5em 0 -2.7em #fff,
    3.5em -3.5em 0 -3em #fff,
    5em 0 0 -2.7em #fff,
    3.5em 3.5em 0 -3em #fff,
    0 5em 0 -2.7em #fff,
    -3.5em 3.5em 0 -3em #fff,
    -5em 0 0 -2.7em #fff,
    -3.5em -3.5em 0 -3em #fff;
          box-shadow: 3em 3em 0 5em #fff inset,
    0 -5em 0 -2.7em #fff,
    3.5em -3.5em 0 -3em #fff,
    5em 0 0 -2.7em #fff,
    3.5em 3.5em 0 -3em #fff,
    0 5em 0 -2.7em #fff,
    -3.5em 3.5em 0 -3em #fff,
    -5em 0 0 -2.7em #fff,
    -3.5em -3.5em 0 -3em #fff;
}
</style>