<template>
    <div>
    <div @mouseleave="productMouseOut" @mouseenter="productMouseOver" class="product">
        <router-link style="text-decoration: none" 
        :to="{ name: 'Product', params: { category: $route.params.category, product: product.article } }"
        v-bind:product="item"
        >
        <div class="img imgOffSet">
            <transition name="photo"><img 
                v-if="!showPhoto" class="image image2" 
                v-lazy="{src: product.images[1].image, 
                    loading: this.$store.state.darkStyle ? 'loadDark.svg' : 'load.svg' }"
                alt="" :class="{whiteBorder : this.$store.state.darkStyle}"
                @error="handleImageError"></transition>
            <transition name="photo"><img v-if="true"
                 class="image image1" 
                 v-lazy="{src: product.images[0].image, 
                    loading: this.$store.state.darkStyle ? 'loadDark.svg' : 'load.svg' }"
                 :class="{whiteBorder : this.$store.state.darkStyle}"
                 @error="handleImageError"></transition>
        </div>
        </router-link>
        <div class="productName">
            <router-link style="text-decoration: none" :to="{ name: 'Product', params: { category: $route.params.category, product: product.article } }"><span class="name" :class="{dark : this.$store.state.darkStyle, darkSimbol : this.$store.state.darkStyle}">{{product.name}}</span></router-link><div 
            @click="addFunc(product)"
            v-if="!addCheck"
            class="add"><div class="add1" :class="{whiteBackground : this.$store.state.darkStyle}"></div><transition name="add"><div
            class="add2" v-if="product.showPrice" :class="{whiteBackground : this.$store.state.darkStyle}"
            ></div></transition></div>
            <div v-else class="addCheck">
                <div class="add">
                    <transition><div v-if="addCheck" class="addCheck1" :class="{white : this.$store.state.darkStyle}"></div></transition>
                    <transition><div v-if="addCheck" class="addCheck2" :class="{white : this.$store.state.darkStyle}"></div></transition>
                    
                </div>
            </div>
        </div>
        <transition>
            <div  class="productPrice">
               
                <transition name="price-fade">
                    <p v-if="product.showPrice" :class="{darkSimbol : this.$store.state.darkStyle}">
                        <span :class="{darkSimbol : this.$store.state.darkStyle}" :style="{'margin-right' : '5px', 'text-decoration': product.discount ? 'line-through' : 'none'}" v-if="product.discount">{{ product.discount }}</span>
                        <span :class="{darkSimbol : this.$store.state.darkStyle}">{{product.price}}</span>
    
                        грн.
                    </p>
                </transition>
            </div>
        </transition>
    </div>
    </div>
</template>

<script>
 /*eslint-disable */
 import VueLazyload from 'vue-lazyload'
 import store from '@/store';
 import AOS from 'aos';
import 'aos/dist/aos.css';

export default {
    components: {
        
    },
    props: {
        product: {
            type: Object,
        },
        category: {
            type: String,
        }
    },
    data() {
        return {
        
           showPhoto: true,
           showPhoto2: false,
           addCheck: false,
           addAnimation: {
            animation: 'addCheck 2s ease'
           },
        }
    },
    methods: {
        handleImageError(event) {
            event.target.src = 'error_img.png'
        },
        addFunc(product) {
            this.addCheck = true;
            store.commit("CART_ADD", product)
        },
        productMouseOver() {
            this.product.showPrice =true
            //alert(event.target.className)
            this.showPhoto = false
            this.showPhoto2 = true
        },
        productMouseOut() {
            this.product.showPrice = false
            this.showPhoto = true
            this.showPhoto2 = false
        },
        priceMobile() {
            console.log(window.innerWidth);
        }
    },
    mounted() {
        window.scrollBy(0, 0)
        //window.addEventListener("resize",this.priceMobile)
        if(window.innerWidth <= 1024) {this.product.showPrice = true}
        //cartLen
        var len = store.state.cart.length
        if(store.state.cart.length < len) {
            console.log(len);   
        }
        //
  
    },
    computed: {
      cartVuex() {
        return store.getters.cart
      },
      
    },
    watch: {
        cartVuex: {
            handler() {
                if(!store.state.cart.find(f => f == this.product)) {
                    this.addCheck = false
                }
            },
            deep: true
        }
    },
}
</script>

<style lang="sass" scoped>
    .product     
        cursor: pointer
        overflow: hidden
        .img
            position: relative
            height: 101%
            img
                width: 100%
                aspect-ratio: 0.85
                border: 1px solid #e0e1e0
            .image2
                position: absolute
                z-index: 1
                height: calc( 100% - 8px)       
                    
            .image1 
                z-index: 2
                height: 100%
                
        .productName
            text-transform: uppercase
            color: grey
            font-size: 16px
            letter-spacing: 0.2em
            height: 25px
            position: relative
            margin: 10px 0 0 0
            display: flex
            justify-content: space-between
            align-items: center
            @media screen and (max-width: 1300px)
                font-size: 14px
            span
                color: grey
            .add
                border: 0px solid black
                width: 20px
                position: relative
                right: 15px
                height: 15px
                padding: 15px

                .add1, .addCheck1
                    height: 1px
                    width: 15px
                    background: grey
                .add2, .addCheck2 
                    height: 15px
                    width: 1px
                    background: grey
                    position: absolute
                    top: 8px
                    left: 22px
                .addCheck1
                    animation: addCheck1 0.5s linear forwards 0.2s
                .addCheck2 
                    animation: addCheck2 0.7s linear forwards
                    
                    

        .productPrice
            margin: 10px 0px 0px 0px
            height: 20px
            p
                color: grey
                font-size: 16px
                position: relative
                bottom: 3px
                span
                    color: grey
                    font-size: 16px
                    letter-spacing: 1px
                &:after
                    content: ''
                    display: block
                    width: 30px
                    height: 30px
                    background: black


.price-fade-enter-active, .price-fade-leave-active
    transition: all 0.5s ease
.price-fade-enter-from, .price-fade-leave-to
    transform: translateY(20px)
    opacity: 0

.price-fade-enter-to, .price-fade-leave-from
    transform: translateY(0px)
    opacity: 1

//photo

.photo-enter-active, .photo-leave-active
    transition: all 0.4s ease
.photo-enter-from, .photo-leave-to 
    opacity: 0
    transform: translateY(0px)
.photo-enter-to, .photo-leave-from
    opacity: 1
    transform: translateY(0px)

.photo2-enter-active, .photo2-leave-active
    transition: all 0.2s ease
.photo2-enter-from, .photo2-leave-to 
    opacity: 0
    transform: translateY(0px)
.photo2-enter-to, .photo2-leave-from
    opacity: 1
    transform: translateY(0px)

.add-enter-active, .add-leave-active
    transition: all 0.3s ease
.add-enter-from, .add-leave-to
    display: block
    transform: rotate(90deg)
.add-enter-to, .add-leave-from 
    display: block
    transform: rotate(0deg)

@keyframes addCheck1
    100%
        transform: rotate(-45deg) translateX(5px) translateY(-1px)
        

@keyframes addCheck2
    25%
        transform: rotate(90deg)
        height: 15px
    100% 
        transform: rotate(135deg) translateX(5px) translateY(2px)
        height: 8px


.dark 
    color: white !important

.whiteBorder 
    border: 1px solid white
.white
    background: white !important
    


</style>