<template>
  <div class="productCart">
    <div class="itemLeft">
        <div class="itemPhoto">
            <router-link style="text-decoration: none" :to="'/product/' + product.article"><img @error="handleImageError" :src="product.images[0].image" alt=""></router-link>
        </div>
        <div class="itemInfo">
            <div class="p">
                <p class="name" :class="{darkSimbol : this.$store.state.darkStyle}">{{product.name}}</p>
                <p v-if="product.size" class="size" :class="{darkSimbol : this.$store.state.darkStyle}">{{product.size}}</p>
                <p v-if="product.robe" class="size" :class="{darkSimbol : this.$store.state.darkStyle}">{{ product.robe }}</p>
            </div>
            <div class="counter">
                <button @click="totalRem(product)" class="rem" 
                :class="[
                this.$store.state.darkVirgin ? {
                    dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
                        } : {
                    darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
                    }
                ]"
                ><span :class="{darkSimbol : this.$store.state.darkStyle}">-</span></button>
                <p class="total" :class="[
                this.$store.state.darkVirgin ? {
                    dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
                        } : {
                    darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
                    }
                ]"
                ><span  :class="{darkSimbol : this.$store.state.darkStyle}">{{product.total}}</span></p>
                <button @click="totalAdd(product)" class="add" 
                :class="[
                this.$store.state.darkVirgin ? {
                    dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
                        } : {
                    darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
                    }
                ]"
                ><span :class="{darkSimbol : this.$store.state.darkStyle}">+</span></button>
            </div>
        </div>
    </div>
    <div class="itemRight">
        <div @click="remove(product)" class="trash" :class="{dark : this.$store.state.darkStyle}">
            <svg v-if="!this.$store.state.darkStyle" fill="#000000"  width="56px" height="56px">
                <path d="M 10 2 L 9 3 L 5 3 L 4 3 L 4 5 L 5 5 L 5 20 C 5 21.093063 5.9069372 22 7 22 L 17 22 C 18.093063 22 19 21.093063 19 20 L 19 5 L 20 5 L 20 3 L 19 3 L 18 3 L 15 3 L 14 2 L 10 2 z M 7 5 L 17 5 L 17 20 L 7 20 L 7 5 z"></path>
            </svg>
            <svg v-else fill="white"  width="56px" height="56px">
                <path d="M 10 2 L 9 3 L 5 3 L 4 3 L 4 5 L 5 5 L 5 20 C 5 21.093063 5.9069372 22 7 22 L 17 22 C 18.093063 22 19 21.093063 19 20 L 19 5 L 20 5 L 20 3 L 19 3 L 18 3 L 15 3 L 14 2 L 10 2 z M 7 5 L 17 5 L 17 20 L 7 20 L 7 5 z"></path>
            </svg>
        </div>
        <p class="price" :class="{darkSimbol : this.$store.state.darkStyle}">
            <span :class="{darkSimbol : this.$store.state.darkStyle}" :style="{'margin-right' : '5px', 'text-decoration' : 'line-through'}" v-if="product.discount">{{ product.total * product.discount }}</span>
            <span :class="{darkSimbol : this.$store.state.darkStyle}">{{product.total * product.price}} грн.</span>
        </p>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import store from '@/store'
export default {
    props: {
        product: Object
    },
    data() {
        return {
            total: 1,
            totalPay: parseInt(this.product.price),
        }
    },
    methods: {
        handleImageError(event) {
            event.target.src = 'error_img.png'
        },
        remove(product) {
            store.commit('CART_REMOVE', product)
        },
        totalAdd(product) {
            store.commit('CART_TOTAL_ADD', product)
            store.commit('CART_TOTAL_CHANGE')
        },
        totalRem(product) {
            store.commit('CART_TOTAL_REMOVE', product)
            store.commit('CART_TOTAL_CHANGE')
        },
    },
    mounted() {
        console.log();
    },
    
}
</script>

<style lang="sass" scoped>
    .productCart
        display: flex
        justify-content: space-between
        background: white

        .itemLeft
            display: flex
            .itemPhoto
              height: 120px
              img
                height: 120px
                aspect-ratio: 0.9
                border: 1px solid #e0e1e0
            .itemInfo
                text-transform: uppercase
                letter-spacing: 0.2em
                padding: 10px 20px 
                display: flex
                flex-direction: column
                justify-content: space-between
                .name
                    font-size: 14px
                    @media (max-width: 550px)
                        font-size: 10px
                .size
                    margin-top: 5px
                    font-size: 10px  
                    @media (max-width: 550px)
                        font-size: 9px
                .counter
                    display: flex
                    button, p
                        width: 30px
                        height: 20px
                        border: 1px solid #e0e1e0
                        background: white
                        display: flex
                        justify-content: center
                        align-items: center
                    button
                       font-size: 18px 
                       cursor: pointer
                       &:active
                        background: #e0e1e0
                    .rem
                    .total
                        text-align: center
                        font-size: 14px
                        position: relative
                        overflow: hidden
                        span
                            position: relative
                            top: 1px
                            overflow: hidden
                    .add
        .itemRight
            display: flex
            flex-direction: column
            height: 100%
            position: relative
            justify-content: space-between
            padding: 0px 0px 0px 0px
            align-items: end
            .trash
                width: 40px
                height: 40px
                opacity: 0.6
                transform: scale(0.7)
                cursor: pointer
                display: flex
                justify-content: center
                position: relative
                top: -5px
                left: 0px
                svg
                    position: relative
                    left: 5px
                    top: 5px
            .price
                position: relative
                right: 12px
                bottom: -2px
                opacity: 0.6
                display: flex
                justify-content: end
                align-items: center
                letter-spacing: 0.15em
                font-size: 14px
                flex-wrap: nowrap
                width: 130px
                @media (max-width: 385px) 
                    width: auto
                    flex-wrap: wrap
                @media (max-width: 550px)
                    font-size: 10px        

</style>