<script>
/*eslint-disable */
import axios from 'axios'
import store from '@/store'

export default {
  data() {
    return {
      api: '1ed79a91c6bf51d9510a65cc98f8b1eb',
      url: `https://api.novaposhta.ua/v2.0/json/`,
      cities: [],
      departs: [],
      streets: [],
      user: {
        radio: store.state.delivery,
        city: store.state.city,
        cityName: null,
        cityRef: null,
        depart: store.state.depart,
        street: store.state.street,
      },
      color_error: 'red',
      virgin: {
        city: true,
        depart: true,
        street: true,
      },
    }
  }, 
  computed: {

  },
  async mounted() {

    this.getCities()

  },
  ///mounted close
  methods: {
    getCities() {
      const requestAddress = {
      apiKey: this.api,
      modelName: "Address",
      calledMethod: "searchSettlements",
      methodProperties: {
        //AreaRef: this.user.area.AreaRef,
        CityName : store.state.user.city,
      }
    }
    axios.post(this.url, requestAddress).then(response => {
      console.log(response);
      this.cities = response.data.data[0].Addresses
      this.user.cityName = this.cities[0].MainDescription
      this.user.cityRef = this.cities[0].Ref
    }).catch(error => {
      console.log(error);
    });
    //
    },
    getDepart() {
      const requestDepart = {
        apiKey: this.api, 
        modelName: "Address",
        calledMethod: "getWarehouses",
        methodProperties: {
          CityName: this.user.cityName  
        }
      }

      axios.post(this.url, requestDepart).then(response => {
        this.departs = response.data.data
        
      }).catch(error => {
        console.log(error);
      })
    },
    // getAddress() {
    //   const requestAddress = {
    //     apiKey: this.api, 
    //     modelName: "Address",
    //     calledMethod: "searchSettlementStreets",
    //     methodProperties: {
    //      StreetName: this.user.cityName,
    //      SettlementRef: this.user.cityRef
    //     }
    //   }

    //   axios.post(this.url, requestAddress).then(response => {
    //     this.streets = response.data.data
    //     console.log(this.streets);
    //   }).catch(error => {
    //     console.log(error);
    //   })
    // },
    returnCity(city) {
      this.user.cityName = city.MainDescription 
      return city.Present
    }
  },
  //methods close
  watch: {
    async '$store.state.user.city'(newValue, oldValue) {
      this.getCities();
    },
    async 'user.cityName'() {
      this.getDepart()
    },
    // async 'user.street'() {
    //   this.getAddress()
    // },
  },
}
</script>

<template>
<div class="adressMain">
     <p class="h3" :class="{darkSimbol : this.$store.state.darkStyle}">
       Доставка
     </p>
     <form
     class="selectAddress">
         <label
         :class="{openRadio: this.$store.state.user.delivery == 'np'}"
         class="radio-block" >
             <input type="radio" name="delivery-method"
             v-model="this.$store.state.user.delivery"
             value="np" checked>
             <span class="method">Нова пошта - самовивіз</span>
         </label>
         <label
         :class="{openRadio: this.$store.state.user.delivery == 'cour'}"
         class="radio-block">
             <input type="radio" name="delivery-method"
             v-model="this.$store.state.user.delivery"
             value="cour">
             <span class="method">Нова пошта - кур'єр</span>
         </label>
     </form>
    <form class="address">
      <label for="city" class="label">
        <p v-show="this.$store.state.user.city" class="p">населений пункт та обл.</p>
         <input id="city" list="city-list" name="city-list"
         placeholder="Населений пункт та обл."
        :class="{paddingAnim : user.city}"
        :style="{ border: (this.$store.state.user.city < 1 && !virgin.city)? '2px solid '+color_error+'' : '' }"
        @click="virgin.city = false"
        v-model="this.$store.state.user.city">
        <datalist id="city-list">
          <option

          v-for="city in cities" :key="city" :value="city.Present" >{{ city.Present }}</option>
        </datalist>
      </label>
      <label v-if="this.$store.state.user.delivery == 'np'" for="depart" class="label">
        <p v-show="user.depart" class="p">відділення нової пошти</p>
         <input id="depart" list="depart-list" name="depart-list" placeholder="Відділення Нової пошти"
        :class="{paddingAnim : user.depart}"
        :style="{ border: (this.$store.state.user.depart < 1 && !virgin.depart)? '2px solid '+color_error+'' : '' }"
        @click="virgin.depart = false"
        v-model="this.$store.state.user.depart">
        <datalist id="depart-list">
          <option 
          v-for="depart in departs" :key="depart"
          :value="depart.Description">{{ depart.Description }}</option>
        </datalist>
      </label>
      <label v-if="this.$store.state.user.delivery == 'cour'" for="street" class="label">
        <p v-show="user.street" class="p">вкажіть вулицю та будинок</p>
         <input id="street-list" list="street-list" name="street-list" placeholder="Адреса для доставки"
        :class="{paddingAnim : user.street}"
        :style="{ border: (this.$store.state.user.street < 1 && !virgin.street)? '2px solid '+color_error+'' : '' }"
        @click="virgin.street = false"
        v-model="this.$store.state.user.street">
        <!-- datalist id="street-list">
          <option 
          v-for="street in streets" :key="street"
          :value="street.Present"
          >
          {{ street.Present }}
          </option>
        </datalist -->
      </label>
    </form>
    
  </div>
</template>

<style lang="sass" scoped>
$primary-color: #00005c
$text-color: mix(#000, $primary-color, 64%)

.adressMain
    width: 100%
    display: flex
    flex-direction: column
    align-items: center
    .h3
      font-size: 14px
      color: #131621
      text-transform: uppercase
      font-family: FuturaBook
      letter-spacing: 2px
      margin-bottom: 10px
    .selectAddress
      display: grid
      grid-column-gap: 15px
      grid-template-columns: 1fr 1fr
      width: 100%
      .radio-block
        height: 50px
        font-size: 13px
        letter-spacing: 1px
        margin-top: 10px
        display: flex
        cursor: pointer
        position: relative
        overflow: hidden
        input
          cursor: pointer
          visibility: hidden
          position: absolute
          &:checked + span
            background-color: mix(#fff, $primary-color, 84%)
            &:before
              box-shadow: inset 0 0 0 0.4375em $primary-color
        .method
          display: flex
          align-items: center
          position: relative
          top: 1px
          letter-spacing: 2px
          color: #131621
          font-size: 11px
          padding: 0.375em 0.75em 0.375em 0.375em
          border-radius: 5px
          width: 100%
          transition: 0.25s ease
          border: 1px solid #e0e1e0
          padding-left: 10px
          background-color: white
          &:hover
            background-color: mix(#fff, $primary-color, 84%)
          &:before
            display: flex
            flex-shrink: 0
            content: ""
            background-color: #fff
            width: 1.5em
            height: 1.5em
            border-radius: 50%
            margin-right: 0.375em
            transition: 0.25s ease
            box-shadow: inset 0 0 0 0.125em $primary-color
            margin-right: 7px
    .address
      width: 100%
      display: grid
      grid-template-columns: 1fr
      position: relative
      .label
        margin-top: 10px
        position: relative
        datalist
          width: 500px !important
          position: absolute !important
          option
        &:before
          content: ''
          width: 10px
          height: 10px
          background: black
          display: none
          position: absolute
          right: 0px
        input
          height: 50px
          width: 100%
          border-radius: 5px !important
          padding-left: 13px
          color: #131621
          font-size: 13px
          letter-spacing: 2px
          border: 1px solid #e0e1e0
          &:focus-visible
            border: 1px solid grey
            outline-color: grey
        .p
          position: absolute
          font-size: 11px
          letter-spacing: 1px
          left: 13px
          top: 2px
          color: grey
        select
          width: 100%
          height: 50px
          border-radius: 5px !important
          padding: 0px 12px
          border: 1px solid #e0e1e0 !important
          letter-spacing: 2px 
          font-size: 11px
          &:focus-visible
            border: 1px solid #e0e1e0 !important
          option
            height: 50px !important
            font-size: 15px
            border: 1px solid #e0e1e0 !important
                
.paddingAnim
  animation: paddingAnim 0.3s forwards

@keyframes paddingAnim
  0%
    padding: 0px 0px 0px 13px
  100%
    padding: 4px 0px 0px 13px
                
          


</style>