/*eslint-disable */
import MainPage from '@/components/MainPage.vue'
import ProductOpened from '@/components/ProductOpened.vue'
import { createRouter, createWebHistory } from 'vue-router'
import store from '@/store'
import GridVue from '@/components/GridVue'
import CheckOut from '@/components/checkout/CheckOut.vue'
import PoliticVue from '@/components/info/PoliticVue.vue'
import ReturnOrder from '@/components/info/ReturnOrder.vue'
import OrderConfirm from '@/components/checkout/OrderConfirm.vue'
import AOS from 'aos';

const routes = [
  {
    path: '/',
    name: 'home',
    component: MainPage,
    meta: { transition: 'opacity'},
    beforeEnter: (to, from, next) => {
      store.commit('REFRESH') 
      console.log(to.path);
      next() 
    },
  },
  {
    path: '/category-:category',
    name: 'Category',
    component: GridVue,
    props: true,
    beforeEnter: (to, from, next) => {
      next()
    },
    meta: { transition: 'opacity-grid'},
  },
  {
    path: '/category-:category/article-:product',
    alias: '/product/:product',
    name: 'Product',
    component: ProductOpened,
    props: true,
    meta: {
      transition: 'opacity-product',
      keepAlive: false
    }
  },
  {
    path: '/checkout',
    name: 'checkout', 
    component: CheckOut,
    props: true,
  },
  {
    path: '/politic',
    name: 'Politic',
    component: PoliticVue,
    meta: { transition: 'opacity-grid'},
  },
  {
    path: '/return',
    name: 'Return',
    component: ReturnOrder,
    meta: { transition: 'opacity-grid'},
  },
  {
    path: '/order/:numberOrder',
    name: 'Order',
    component: OrderConfirm,
    mata: {transition: 'opacity-product',},
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (to.meta.scrollToTop) {
      return { x: 0, y: 0 };
    }
  }
});

router.beforeEach((to,from, next) => {
  if(store.state.cartShow) {store.commit("CART_SHOW")}
  store.commit("VIRGIN")
  if (from.name == 'Product') {
   from.meta.transition = 'leave'
  }
  next()
})

router.afterEach((to,from) => {
  if (to.name == 'Category') {
    AOS.refresh();

  }
})


export default router
