<template>
  <div class="cartMain"    
   :class="[
      this.$store.state.darkVirgin ? {
        dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
      } : {
        darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
      }
    ]"
    >
    <div class="cartContent">
      <div class="cartHeader">
        <p :class="{darkSimbol : this.$store.state.darkStyle}">Кошик</p>
        
        <div @click="closeCart" class="cartCross"></div>
      </div>
      <div v-if="empty" class="empty" :class="{borderBottom : this.$store.state.darkStyle}"><p :class="{emptyDark : this.$store.state.darkStyle}">Кошик порожній</p></div>
      <div v-if="cartTrue" class="cart">
        <transition-group name="list" tag="ProductCart">
        <ProductCart :product="product" v-for="product in cart" :key="product" class="cartItem" 
        :class="[
            this.$store.state.darkVirgin ? {
              dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
            } : {
              darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
            }
          ]"
        >
          
        </ProductCart>
        </transition-group>
      </div>
    </div>
    <div class="cartBottom"     
    :class="[
      this.$store.state.darkVirgin ? {
        dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
      } : {
        darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
      }
    ]">
      <div class="totalInfo" >
        <p class="totalItems" :class="{darkSimbol : this.$store.state.darkStyle}">Товарів: {{cartTotal}}</p>
        <p class="totalPay" :class="{darkSimbol : this.$store.state.darkStyle}"> {{cartTotalPay}} грн.</p>
      </div>
      <div class="cartButtons">
        <button class="refresh" @click="refresh"     
        :class="[
          this.$store.state.darkVirgin ? {
            dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
            } : {
              darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
            }
          ]"
    >
          <svg v-if="this.$store.state.darkStyle" fill="white" height="50px" x="0px" y="0px" viewBox="0 0 384.97 384.97" style="enable-background:new 0 0 384.97 384.97;" xml:space="preserve">
              <path id="Loop" d="M360.909,17.934H24.061C10.767,17.934,0,28.713,0,41.995V258.54c0,13.293,10.767,24.061,24.061,24.061h60.152
              c7.711,0,12.03-5.45,12.03-12.03c0-6.581-4.09-12.187-12.03-12.187H38.738c-8.036,0-14.557-6.52-14.557-14.557V57.093
              c0-8.036,6.52-14.557,14.557-14.557l307.627-0.373c8.036,0,14.557,6.52,14.557,14.557v187.107c0,8.036-6.52,14.557-14.557,14.557
              H209.556l62.413-63.303c4.692-4.752,4.692-12.439,0-17.191c-4.704-4.74-12.319-4.74-17.011,0l-83.009,84.2
              c-4.692,4.74-4.692,12.439,0,17.191c0,0,0,0,0.012,0l82.997,84.2c4.692,4.74,12.319,4.74,17.011,0
              c4.692-4.752,4.692-12.439,0-17.179l-62.774-63.688h151.714c13.293,0,24.061-10.767,24.061-24.061V42.007
              C384.97,28.713,374.203,17.934,360.909,17.934z"/>
          </svg>
          <svg v-else fill="grey" height="50px" x="0px" y="0px" viewBox="0 0 384.97 384.97" style="enable-background:new 0 0 384.97 384.97;" xml:space="preserve">
              <path id="Loop" d="M360.909,17.934H24.061C10.767,17.934,0,28.713,0,41.995V258.54c0,13.293,10.767,24.061,24.061,24.061h60.152
              c7.711,0,12.03-5.45,12.03-12.03c0-6.581-4.09-12.187-12.03-12.187H38.738c-8.036,0-14.557-6.52-14.557-14.557V57.093
              c0-8.036,6.52-14.557,14.557-14.557l307.627-0.373c8.036,0,14.557,6.52,14.557,14.557v187.107c0,8.036-6.52,14.557-14.557,14.557
              H209.556l62.413-63.303c4.692-4.752,4.692-12.439,0-17.191c-4.704-4.74-12.319-4.74-17.011,0l-83.009,84.2
              c-4.692,4.74-4.692,12.439,0,17.191c0,0,0,0,0.012,0l82.997,84.2c4.692,4.74,12.319,4.74,17.011,0
              c4.692-4.752,4.692-12.439,0-17.179l-62.774-63.688h151.714c13.293,0,24.061-10.767,24.061-24.061V42.007
              C384.97,28.713,374.203,17.934,360.909,17.934z"/>
          </svg>
        </button>
        <button class="confirm"  
        @click="goToCheckout"   
        :class="[
            this.$store.state.darkVirgin ? {
              dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
            } : {
              darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
            }
          ]"
          ><span :class="{darkSimbol : this.$store.state.darkStyle}">Прийняти</span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
/*eslint-disable */
import store from '@/store';
import ProductCart from './ProductCart.vue';


export default {
  components: {
    ProductCart,
    ProductCart
},
  beforeCreate() {
    store.commit("VIRGIN")
  },
  created() {
    store.commit('CART_INIT')

  },
  mounted() {
    
  },
  data() {
    return {
      cartTrue: true,
      empty: false,
    }
  },
  methods: {
    closeCart() {
      store.commit("CART_SHOW")
    },
    refresh() {
      store.commit("CART_REFRESH")
    }, 
    goToCheckout() {
      this.$router.push('/checkout');
    }
  },
  computed: {
    cart() {
      if(this.$store.getters.cart == null) this.cartTrue = false; 
      if (store.state.cart.length == 0) this.empty = true
      return store.getters.cart
    },
    cartVuex() {
        return store.getters.cart
      },
    cartTotalPay() {
          return store.getters.cartTotalPay
      },
    cartTotal() {
      return store.getters.cartTotal
    }
  },
  watch: {
    cartVuex: {
      handler() {
        if (store.state.cart.length == 0) this.empty = true
        else this.empty = false
      },
      deep: true
    }
  }
}
</script>

<style lang="sass" scoped>
  .cartMain
    width: 500px
    height: 100%
    background-color: white
    position: fixed
    right: 0px
    top: 0px
    z-index: 10
    border-left: 1px solid #e0e1e0
    display: flex
    flex-direction: column
    justify-content: space-between
    @media screen and (max-width: 480px)
      width: 100%
    .cartContent
      display: flex
      flex-direction: column
      height: 100%
      .cartHeader 
        width: 100%
        height: 77px
        border-bottom: 1px solid #e0e1e0
        display: flex
        align-items: center
        justify-content: space-between
        padding: 0px 0 0 15px
        p
          font-size: 18px
          text-transform: uppercase
          font-family: FuturaBook
          letter-spacing: 0.2em
          color: grey
        .cartCross
          width: 40px
          height: 40px
          position: absolute
          right: 20px
          top: 20px
          cursor: pointer
          &:after, &:before
            content: ''
            display: block
            width: 20px
            height: 2px
            background-color: grey
            position: absolute
            top: 15px
            right: 7px
          &:after
            transform: rotate(45deg)
          &:before
            transform: rotate(-45deg)
      .empty 
        text-transform: uppercase
        letter-spacing: 0.2em
        color: #e0e1e0
        opacity: 0.7
        width: 100% 
        display: flex
        justify-content: center
        align-items: center
        position: relative
        border-bottom: 1px solid #e0e1e0
        animation: empty 0.5s
        font-size: 14px
        height: 70px
      .cart
        height: calc( 100% - 180px )
        overflow-y: auto
        padding: 20px 0px 0px 0px
       
        .cartItem
          height: 130px
          width: 100%
          display: flex
          justify-content: space-between
          margin-bottom: 20px
          border-bottom: 1px solid #ededed
          align-items: center
          padding: 5px 13px 25px 15px
          
  .cartBottom
    width: 100%
    height: 100px
    position: absolute
    bottom: 0px
    border-top: 1px solid #ededed
    display: flex
    flex-direction: column
    padding: 5px 10px
    justify-content: center
    align-items: center
    overflow: hidden
    background-color: white
    .totalInfo
      display: flex
      text-transform: uppercase
      font-size: 11px
      width: 425px
      position: relative
      bottom: 5px
      justify-content: space-between
      padding:  0px 5px
      letter-spacing: 0.15em
      @media screen and (max-width: 450px)
        width: 100%
      .totalItems
        
      .totalPay
        text-transform: lowercase
    .cartButtons
      display: flex
      width: 100%
      justify-content: center
      position: relative
      .refresh
        width: 75px
        height: 50px
        border-right: 0px !important
        svg
          position: relative
          top: 2px
          transform: scale(0.5)
      .confirm
        width: 350px
        height: 50px
        text-transform: uppercase
        letter-spacing: 0.2em
        font-size: 12px
      .confirm, .refresh 
        background-color: white
        border-radius: 0px
        border: 1px solid #e0e1e0
        cursor: pointer

.emptyDark
  color: white
  opacity: 1


.list-move, .list-leave-active
  transition: all 0.4s ease
.list-enter-active
  transition: all 0.4s ease
.list-enter-from , .list-leave-to
  transform: translateX(1000px)
  opacity: 0
.list-enter-to, .list-leave-from
  transform: translateX(0px)
  opacity: 1

.list-leave-active 
  position: absolute

@keyframes empty
  from 
    opacity: 0
  to
    opacity: 0.7        

</style>