<template>
  <div class="productOpened"
  :style="fullSize ? {justifyContent: 'center', height: '100%'} : true"
  >
    <div class="left" 
    :style="fullSize? {
      height: '100%'
    } : true "
   
    >
      <ProductSlider
      :images="images"
      ></ProductSlider>  
    </div>
    <div v-if="!fullSize " class="right">
      <div class="before_load" v-if="!load"></div>
      <div v-if="load" class="info">
        <div class="name">
          <p :class="{darkSimbol : this.$store.state.darkStyle}">{{ item.name }}</p>
        </div>
        <p class="price" :class="{darkSimbol : this.$store.state.darkStyle}">
          <span 
          :class="{darkSimbol : this.$store.state.darkStyle}"
          :style="{'margin-right' : '10px', 'text-decoration' : 'line-through'}"
          v-if="item.discount">{{ item.discount }}</span>
          <span :class="{darkSimbol : this.$store.state.darkStyle}">{{ item.price }} </span>
          грн.
        </p>
      
        <div class="bar">
          <div class="bar-info bar-item" >
            <span
            @click="infoClick()" :class="{darkSimbol : this.$store.state.darkStyle}"
            >Iнфо</span>
            <transition name="bottomAnim"><div class="bottom" v-if="info" :style="this.$store.state.darkStyle ? {background : 'white'} : true"></div></transition>
          </div>
          <div class="notes bar-item" >
            <span
            
            @click="notesClick()" :class="{darkSimbol : this.$store.state.darkStyle}"
            >Опис</span>
            <transition name="bottomAnim"><div class="bottom" v-if="notes" :style="this.$store.state.darkStyle ? {background : 'white'} : true"></div></transition>
          </div>
        </div>
       <transition name="infoAnim" mode="out-in">
       <div class="blockInfo" v-if="info">
        
        <div class="infoBlock">
          <div class="category char" :class="{darkSimbol : this.$store.state.darkStyle}" data-aos="flip-up" :data-aos-delay="width > 1050 ? 450 : 50" :data-aos-duration="duration_anim">
            Категорія: <div class="form" :class="{ darkSimbol : this.$store.state.darkStyle}">{{ item.category }}</div>
          </div>
          <div v-if="item.material" class="material char" :class="{darkSimbol : this.$store.state.darkStyle}" data-aos="flip-up" :data-aos-delay="width > 1050 ? 500 : 50" :data-aos-duration="duration_anim">
            Матеріал: <div class="form" :class="{ darkSimbol : this.$store.state.darkStyle}">{{ item.material }}</div>
          </div>
          <div v-if="item.brand" class="brand char" :class="{ darkSimbol : this.$store.state.darkStyle}" data-aos="flip-up" :data-aos-delay="width > 1050 ? 600 : 50" :data-aos-duration="duration_anim">
            Бренд: <div class="form" :class="{darkSimbol : this.$store.state.darkStyle}">{{  item.brand }}</div>
          </div>
          <div v-if="filler" class="filler char" :class="{darkSimbol : this.$store.state.darkStyle}" data-aos="flip-up" :data-aos-delay="width > 1050 ? 600 : 100" :data-aos-duration="duration_anim">
            Наповнювач: <div class="form" :class="{darkSimbol : this.$store.state.darkStyle}">{{ item.filler }}</div>
          </div>
          <div v-if="item.robe_category" class="robe_category char" :class="{ darkSimbol : this.$store.state.darkStyle}" data-aos="flip-up" :data-aos-delay="width > 1050 ? 600 : 100" :data-aos-duration="duration_anim">
            Категорія одягу: <div class="form" :class="{ darkSimbol : this.$store.state.darkStyle}">{{ item.robe_category }}</div>
          </div>
          <div v-if="item.article" class="towel char" :class="{ darkSimbol : this.$store.state.darkStyle}" v-on:click="copyToClipboard(item.article)" data-aos="flip-up" :data-aos-delay="width > 1050 ? 650 : 100" :data-aos-duration="duration_anim">
            Артикль : <div class="form" :class="{darkSimbol : this.$store.state.darkStyle}">{{ item.article }}</div>
          </div>
        </div>

        <div class="sizeBlock">
          <div v-if="item.robe" class="robe charSize" :class="{darkSimbol : this.$store.state.darkStyle}" :data-aos-delay="width > 1050 ? 700 : 100" data-aos="flip-up" :data-aos-duration="duration_anim">
            Розмір одягу: <div class="form" :class="{ darkSimbol : this.$store.state.darkStyle}">{{ item.robe }}</div>
          </div>
          <div class="size charSize" v-if="item.size" :class="{ darkSimbol : this.$store.state.darkStyle}" :data-aos-delay="width > 1050 ? 700 : 100" data-aos="flip-up" :data-aos-duration="duration_anim">
            Загальний розмір: <div class="form" :class="{ darkSimbol : this.$store.state.darkStyle}">{{ item.size }}</div>
          </div>
          <div v-if="item.pillow_case" class="pillow charSize" :class="{ darkSimbol : this.$store.state.darkStyle}" :data-aos-delay="width > 1050 ? 750 : 100" data-aos="flip-up" :data-aos-duration="duration_anim">
            <span v-if="item.category == 'Постель'" :class="{darkSimbol : this.$store.state.darkStyle}">Наволочка :</span>
            <span v-if="item.category == 'Подушки'" :class="{darkSimbol : this.$store.state.darkStyle}">Подушка :</span>
            <div class="form" :class="{ darkSimbol : this.$store.state.darkStyle}">{{ item.pillow_case }} </div>
          </div>
          <div v-if="item.sheet_size" class="sheet charSize" :class="{ darkSimbol : this.$store.state.darkStyle}" :data-aos-delay="width > 1050 ? 800 : 100" data-aos="flip-up" :data-aos-duration="duration_anim">
            Простирадло: <div class="form" :class="{darkSimbol : this.$store.state.darkStyle}">{{ item.sheet_size }}</div>
          </div>
          <div v-if="item.duvet_size" class="duvet charSize" :class="{ darkSimbol : this.$store.state.darkStyle}" :data-aos-delay="width > 1050 ? 850 : 100" data-aos="flip-up" :data-aos-duration="duration_anim">
            <span :class="{darkSimbol : this.$store.state.darkStyle}" v-if="item.category === 'Постель'">Пододеяльник :</span>
            <span :class="{ darkSimbol : this.$store.state.darkStyle}" v-if="item.category === 'Одеяло'">Одеяло :</span>
            <span :class="{darkSimbol : this.$store.state.darkStyle}" v-if="item.category === 'Плед'">Плед :</span>
            <div class="form" :class="{ darkSimbol : this.$store.state.darkStyle}">{{ item.duvet_size }} </div>
          </div>
          <div v-if="item.rug" class="rug charSize" :class="{ darkSimbol : this.$store.state.darkStyle}" :data-aos-delay="width > 1050 ? 750 : 100" data-aos="flip-up" :data-aos-duration="duration_anim">
            Розмір килимка: <div :class="{ darkSimbol : this.$store.state.darkStyle}" class="form">{{ item.rug }}</div>
          </div>
          <div v-if="item.towel" class="towel charSize" :class="{ darkSimbol : this.$store.state.darkStyle}" :data-aos-delay="width > 1050 ? 750 : 100" data-aos="flip-up" :data-aos-duration="duration_anim">
            Розмір рушника: <div :class="{ whiteBorder : this.$store.state.darkStyle, darkSimbol : this.$store.state.darkStyle}" class="form">{{ item.towel }}</div>
          </div>
        </div>
       </div>
      </transition>
      <transition name="infoAnim">
       <div v-show="notes" class="noteText" :class="{darkSimbol : this.$store.state.darkStyle}">
        <span v-show="item.notes" :class="{darkSimbol : this.$store.state.darkStyle}">{{ item.notes }}</span>
        <span v-show="item.notes == null" :class="{darkSimbol : this.$store.state.darkStyle}">
          Опис відсутній
        </span>
       </div>
      </transition>

      </div>
      <div class="addToCart" :class="{whiteBorder : this.$store.state.darkStyle}" v-if="load">
        <button class="add"    
        @click="addFunc(item)"
        :class="[
          this.$store.state.darkVirgin ? {
            dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
          } : {
            darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
          }
        ]"
    >
          <span :class="{darkSimbol : this.$store.state.darkStyle}">до кошика</span>
        </button>
      </div>
    </div>
    <div class="before_load_mob" v-if="!load && isWindowWidthMob"></div>
  </div>
</template>

<script>
 /*eslint-disable */
 import AOS from 'aos';
import store from '@/store'
import axios from 'axios';
import ProductSlider from './ProductSlider.vue'

export default {
  props: {
    product: Number
  },  
  computed: {
    widthComputed() {
      window.innerWidth > 1050 ? this.mobile = 1 : this.mobile = 0
    },
    isWindowWidthGreaterThan1050() {
      window.innerWidth > 1050 ? true : false
    },
    isWindowWidthMob() {
      window.innerWidth < 1050 ? true : false
    }
  },
  data() {
  return {
    screenWidth: window.innerWidth,
    anim: 0,
    chars: 0,
    infoAll: null,
    item: null,
    images: [],
    load: false,
    leftHeight: null,
    counter: 1,
    fullSize: false,
    info: true,
    notes: false,
    table: false,
    duration_anim: 1000,
  }
},
components: {
  ProductSlider,
},
  created() {
    this.productData()
  },
  mounted() {
    window.scrollTo(0, 0);
    AOS.init()
    
    this.infoAll = document.querySelectorAll('.char').length;
    window.addEventListener('resize', this.handleScreenResize);
    setTimeout(() => {
      this.handleScreenResize()
    }, 500);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.handleScreenResize);
    console.log('destory ');
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleScreenResize);
  },
  methods: {
    handleScreenResize() {
      this.screenWidth = window.innerWidth;
      this.rightHeight()
    },
    handleImageError(event) {
            event.target.src = url('error_img.png');
        },
    addFunc(product) {
            store.commit("CART_ADD", product)
        },
    infoClick() {
      this.notes = false; this.table = false; setTimeout(() => { this.info = true; }, 450)
    },
    notesClick() {
      this.info = false; this.table = false; setTimeout(() => { this.notes = true; }, 450)
    },
    tableClick() {
      this.info = false; this.notes = false; setTimeout(() => { this.table = true; }, 450)
    },
    productData() {
    axios.get(`${store.state.url}/api/products/?article=${String(this.product)}`)
      .then(response => {
          this.item = response.data[0];
          this.item.total = 1
          for (let img of response.data[0].images) {
            this.images.push(String(img.image))
          }
          //this.take_chars(response.data[0])
          this.load = true;
        }).then(() => {
          this.handleScreenResize()
        })
      .catch(error => {
        console.log(error);
      });
  },
  rightHeight() {
    const left = document.querySelector('.left');
    const right = document.querySelector('.right');
    this.screenWidth > 1050 ? right.style.height = left.offsetHeight + 'px' : right.style.height = 'auto'
  },
  infoHight() {
    const info = document.querySelector('.info')
    //info.style.minHeight = info.offsetHeight  + 'px'
  },
  copyToClipboard(text) {
    navigator.clipboard.writeText(text)
      .then(() => {
        console.log(text + 'copy');
      })
      .catch((error) => {
        console.error('Failed to copy text: ', error);
      });
  },
  },
  watch: {
    '$store.state.darkStyle': function() {
       this.load = false
       setTimeout(() => this.load = true, 100)
    },
    '$refs.left.offsetHeight'(newVal) {
    this.leftHeight = newVal;
    },
  },
  beforeRouteLeave(to, from) {
    document.querySelector('.swiper-pagination').style.display = 'none';
  },
}
</script>

<style lang="sass" scoped>
$primary-color: #00005c
$text-color: mix(#000, $primary-color, 64%)
.productOpened
  justify-content: center
  padding: 25px 0px
  display: flex
  box-sizing: border-box
  @media (max-width: 1050px)
    flex-direction: column
    padding: 0px
  .left
    height: 100%
    border: 1px solid #e0e1e0
    position: relative
    right: 40px
    @media (max-width: 1050px)
      right: 0px
  .right
    overflow: hidden
    display: flex
    flex-direction: column
    justify-content: space-between
    @media (max-width: 1050px)
      margin: 50px 0 0 0
    .infoBlock, .sizeBlock
      display: grid
      grid-row-gap: 20px
      flex-direction: column
      @media (min-width: 1051px) and (max-width: 1199px)
        grid-row-gap: 10px
    .info
      padding: 0px 0px 0px 20px
      display: flex
      flex-direction: column
      grid-gap: 5px
      @media (min-width: 1050px)
        width: 350px
      @media (max-width: 1050px)
        display: flex
        padding: 0px 13% 0px 13%
      @media (max-width: 650px)
        padding: 0px 5% 0px 5%
      .name
        text-transform: uppercase
        word-spacing: 1px
        letter-spacing: 3px
        font-size: 22px
        font-family: Futura
        margin-bottom: 7px
      .bar 
        display: flex
        margin: 15px 0 20px 0
        justify-content: space-between
        text-transform: none
        letter-spacing: 2px
        .bar-item
          display: flex
          flex-direction: column
          .bottom
            width: 100%
            height: 1px
            background: black
            flex-direction: column
            
          span 
            font-size: 17px
            cursor: pointer
      .price
        margin-left: 3px
        font-size: 17px
        letter-spacing: 2px
      .blockInfo
        display: grid
        flex-direction: column
        grid-row-gap: 30px
        @media (max-width: 1050px)
          width: 100%
          display: flex
          flex-direction: row
        @media (max-width: 1050px)
          flex-direction: column
        .char, .charSize
          display: flex
          text-transform: none
          letter-spacing: 2px
          align-items: center
          width: 100%
          justify-content: space-between
          .form
            border: 1px solid #e0e1e0
            margin-left: 5px
            text-transform: none
            letter-spacing: 2px
            padding: 5px 10px
            cursor: pointer
            user-select: text
            cursor: text
            &:hover
              border: 1px solid 
              color: black
        .sizeBlock
          @media (max-width: 1050px)
            margin: 0px
            
    .addToCart
      width: 350px
      min-width: 350px
      height: 60px
      min-height: 60px
      display: flex
      justify-content: space-between
      border: 1px solid grey
      @media (max-width: 1050px)
        margin: 50px 0 50px 20px
      @media (max-width: 650px)
        align-self: center
        margin: 50px 0 50px 0
      @media (min-width: 651px) and (max-width: 1049px)
        margin-left: 13%
      .add
        border: none
        color: grey
        width: 100%
        text-transform: uppercase
        letter-spacing: 4px
        background: white
        word-spacing: 5px
        font-size: 16px
        font-family: Futura
        cursor: pointer
        @media (max-width: 650px)
          font-size: 15px
        &:hover
          border: 1px solid 
          color: black
          background-color: mix(#fff, $primary-color, 84%) !important
    .noteText
      width: 330px
      overflow: hidden
      span 
        letter-spacing: 1px
      @media (max-width: 1050px)
        width: auto

.before_load
  width: 350px
  @media (max-width: 1050px) 
    height: 500px
.whiteBorder 
  border: 1px solid white

.bottomAnim-enter-active, .bottomAnim-leave-active
  transition: all 0.3s ease
.bottomAnim-enter-from, .bottomAnim-leave-to
  opacity: 0
.bottomAnim-leave-from, .bottomAnim-enter-to
  opacity: 1  
  
.infoAnim-enter-active, .infoAnim-leave-active
  transition: all 0.4s ease
.infoAnim-enter-active
@media (min-width: 1051px)
  .infoAnim-leave-from, .infoAnim-enter-to
    transform: translateX(0px)
  .infoAnim-leave-to, .infoAnim-enter-from
    transform: translateX(350px)

@media (max-width: 1050px)
  .infoAnim-leave-from, .infoAnim-enter-to
    transform: translateX(0%)
  .infoAnim-leave-to, .infoAnim-enter-from
    transform: translateX(150%)

</style>