<script>
/* eslint-disable */ 
import AOS from 'aos';
import CheckAddress from '@/components/checkout/CheckOutAddres.vue'
import CheckOutPay from '@/components/checkout/CheckOutPay.vue'
import CheckOutCart from '@/components/checkout/CheckOutCart.vue'
import store from '@/store';
export default {
  components: {
    CheckAddress,
    CheckOutPay,
    CheckOutCart,
  },
  data() {
    return {
      width: 0,
      firstInvalid: false,
      lastInvalid: false,
      numberInvalid: false,
      emailInvalid: false,
      user: {
        info: true,
        firstName: store.state.user.firstName,
        lastName: store.state.user.lastName,
        number: store.state.user.number,
        email: store.state.user.email,
      },
      color_error: 'red',
      color_green: 'green',
      virgin: {
        firstName: true,
        lastName: true,
        number: true,
        email: true,
      },
    }
  }, 
  mounted() {
    AOS.init()

  },
  methods: {
    valid() {
      if (store.state.user.firstName) {
        store.state.user.firstName.length < 2 ? this.firstInvalid = true : false
      } else {this.firstInvalid = true}
      if (store.state.user.lastName) {
        store.state.user.lastName.length < 2 ? this.lastInvalid = true : false
      } else {this.lastInvalid = true}
      if (store.state.user.number) { 
        store.state.user.number < 10 ? this.numberInvalid = true : false
      } else {this.numberInvalid = true}
      this.emailInvalid = !/^\S+@\S+\.\S+$/.test(store.state.user.email);
      //console.log(this.firstInvalid);
      //console.log(this.emailInvalid);
    },
  },
  computed: {
    windowWidth() {
      if (window.innerWidth > 1050) return 400
      if (window.innerWidth < 1049) return 100
    },
    valid_firstName() {
      if (this.$store.state.user.firstName !== null && this.$store.state.user.firstName !== undefined) {
        const hasValidLength = this.$store.state.user.firstName.length !== null && this.$store.state.user.firstName.length 
        !== undefined && this.$store.state.user.firstName.length < 2;

        if (hasValidLength && !this.virgin.firstName) {
          return `2px solid ${this.color_error}`;
        } else {
          return `2px solid ${this.color_green}`; // Добавлено условие для зеленой границы
        }
      }

      return '';
    },
    valid_lastName() {
      if (this.$store.state.user.lastName !== null && this.$store.state.user.lastName !== undefined) {
        const hasValidLength = this.$store.state.user.lastName.length !== null && this.$store.state.user.lastName.length 
        !== undefined && this.$store.state.user.lastName.length < 2;
        return hasValidLength && !this.virgin.lastName ? `2px solid ${this.color_error}` : `2px solid ${this.color_green}`;
      }
      return '';
    },

    valid_number() {
      const userNumber = this.$store.state.user.number;
      const isVirgin = !this.virgin.number;

      if (userNumber !== null && userNumber !== undefined) {
        const hasValidLength = userNumber.length !== null && userNumber.length !== undefined && userNumber.length < 9;
        return hasValidLength && isVirgin ? `2px solid ${this.color_error}` : `2px solid ${this.color_green}`;
      }

      return '';
    },

    valid_email() {
      const userEmail = this.$store.state.user.email;
      const isVirgin = !this.virgin.email;

      if (userEmail !== null && userEmail !== undefined) {
        const hasValidLength = userEmail.length !== null && userEmail.length !== undefined && userEmail.length >= 5;
        const hasAtSymbol = userEmail.includes('@'); // Проверка наличия знака "@"

        return (hasValidLength && hasAtSymbol && isVirgin) ? `2px solid ${this.color_green}` : `2px solid ${this.color_error}`;
      }

      return '';
    },
  },
  watch: {
  '$store.state.user_valid'(newValue) {
    if (newValue === 1) {
      this.valid();
    }
  }
},
}
</script>

<template>
  <div class="checkout">
    <div class="checkInfo">
      <form class="formInfo" data-aos="fade-right" data-aos-delay="100">
        <p class="h3" :class="{darkSimbol : this.$store.state.darkStyle}">Контактная информация</p>
        <div class="mb-3 names">


          <div class="first">
            <p v-show="this.$store.state.user.firstName">Ім'я</p>
            <input 
              v-model="this.$store.state.user.firstName" autocomplete="off"
              :class="{paddingAnim : this.$store.state.user.firstName}"
              :style="{ 
                border: valid_firstName
              }"
              placeholder="Ім'я" type="text" class="form-control" id="firstName" name="firstName" required
              @click="virgin.firstName = false"
              @focus="virgin.firstName = true"
              >
              <div
              :style="{color: color_error}"
              class="error" v-if="false">
                кількість символів від 1 до 25
            </div>
          </div>


          <div class="last">
            <p v-show="this.$store.state.user.lastName">Прізвище</p>
            <input 
            v-model="this.$store.state.user.lastName" autocomplete="off"
            :class="{paddingAnim : this.$store.state.user.lastName}"
            :style="{ 
              border: valid_lastName
            }"
            placeholder="Прізвище" type="text" class="form-control" id="lastName" name="lastName" required
            @click="virgin.lastName = false"
            >
            <div class="error" v-if="false"
              :style="{color: color_error}"
              >
              кількість символів від 1 до 25
            </div>
          </div>
        </div>


        <div class="mb-3">
          <p v-show="this.$store.state.user.number" class="p">номер телефону</p>
          <input
           v-model="this.$store.state.user.number" 
           :class="{paddingAnim : this.$store.state.user.number}"
           :style="{ 
              border: valid_number
            }"
           pattern="[\+]\d{2}\s[\(]\d{3}[\)]\s\d{3}[\-]\d{2}[\-]\d{2}"
           placeholder="Номер телефону" type="tel" class="form-control" id="phone" name="phone" required
           @click="virgin.number = false"
           >
           <div
           :style="{color: color_error}"
           class="error" v-if="false">
            введіть номер телефону
          </div>
        </div>


        <div class="mb-3">
          <p v-show="this.$store.state.user.email" class="p">email</p>
          <input 
          v-model="this.$store.state.user.email" 
          :class="{paddingAnim : this.$store.state.user.email}"
          :style="{ 
              border: valid_email
            }"
           placeholder="email" type="email" class="form-control" id="email" name="email" required
           @click="virgin.email = false"
           >
           <div
           :style="{color: color_error}"
            class="error" v-if="false">
            введіть email
            </div>
        </div>


      </form>
      <div class="formAddress" data-aos="fade-left" data-aos-delay="200">
        <CheckAddress></CheckAddress>
      </div>
      <div class="checkOutCart" data-aos="fade-right" data-aos-delay="300">
        <CheckOutCart>
        </CheckOutCart>
      </div>
      <div class="pay" data-aos="fade-left" :data-aos-delay="windowWidth">
        <CheckOutPay>
        </CheckOutPay>
      </div>
    </div>
  </div>
</template>

<style lang="sass" scoped>
.checkout
  display: flex
  width: 100%
  padding: 50px 50px 50px 50px
  flex-direction: column
  align-items: center
  overflow: hidden
  @media (max-width: 1050px)
    padding: 25px 10px
  .checkInfo
    display: grid
    grid-template-columns: 500px 500px
    grid-column-gap: 15px
    grid-row-gap: 40px
    color: #131621
    @media (max-width: 1050px)
      grid-template-columns: 500px
    @media (max-width: 515px)
      grid-template-columns: auto 
    .formInfo, .formAddress, .pay, .checkOutCart
      
    .formInfo
      display: flex
      flex-direction: column
      align-items: center
      .h3
        font-size: 14px
        color: #131621
        text-transform: uppercase
        font-family: FuturaBook
        letter-spacing: 2px
        margin-bottom: 10px
      .mb-3
        width: 100%
        margin-top: 10px
        position: relative
        letter-spacing: 1px
        .error
          position: absolute
          top: 5px
          font-size: 11px
          right: 5px
        input
          width: 100%
          height: 50px
          padding: 0px 0px 0px 13px
          border-radius: 5px
          border: 1px solid #e0e1e0
          font-size: 13px
          letter-spacing: 2px
          &:focus
            border: 1px solid grey
            outline-color: grey
        .p
          position: absolute
          font-size: 11px
          left: 13px
          top: 2px
          color: grey


            
      .names
        display: flex
        justify-content: space-between
        .first, .last
          position: relative
          width: 49%
          p
            position: absolute
            font-size: 11px
            top: 2px
            left: 13px
            color: grey
          input
            width: 100%
  
.paddingAnim
  animation: paddingAnim 0.3s forwards

@keyframes paddingAnim
  0%
    padding: 0px 0px 0px 13px
  100%
    padding: 4px 0px 0px 13px

</style>