<template>
    <div class="categories_content">
       <div v-for="(category, index) in categories" :key="category" class="category_item"
       >
            <router-link 
            :to="{path: '/category-' + category.url, params: {category: category.category}}"
            class="category" :style="{
            backgroundImage: `url(${category.image})`,
            ...this.$store.state.darkStyle ? {border: '1px solid #e0e1e0'} : {border: '1px solid grey'},
            textDecoration: 'none'
            }" 
            :data-aos="index % 2 == 0 ? 'fade-up' : 'fade-up'"
            :data-aos-duration="300"
            :data-aos-delay="(index % this.$store.state.columnsCategory) * 50">
                    <div class="category_button"
                    :style="{background: this.$store.state.dark}"
                    >
                        <p>{{ category.category }}
                        {{  }}</p>
                    </div>
            </router-link>
        </div>
    </div>
</template>

<script>
 /*eslint-disable */
import { mapActions, mapGetters } from 'vuex';
import AOS from 'aos';
import 'aos/dist/aos.css';
 import store from '@/store'

export default {
      data() {
        return {

        };
    },
    created() {

    },
    
    mounted() {
        this.$store.dispatch("GET_CATEGORIES").then(() => {
            AOS.init()
            AOS.refreshHard()
        })
        window.scrollTo(0, 0);
       
    },
    computed: {
        ...mapGetters([
            'categories'
        ]),
        ...mapActions([
            'GET_CATEGORIES'
        ]),

    },
    methods: {

    }

}

</script>

<style lang="sass" scoped>
    .categories_content 
        display: grid
        grid-template-columns: 1fr 1fr 1fr 1fr
        grid-column-gap: 20px
        grid-row-gap: 30px
        overflow: hidden
        box-sizing: border-box
        justify-content: center
        padding: 0px 20px 0px 20px
        @media (max-width: 1300px)
            grid-template-columns: 1fr 1fr 1fr
        @media (max-width: 900px)
            grid-template-columns: 1fr 1fr
        @media (max-width: 550px)
            grid-template-columns: 1fr
        .category_item
            .category
                min-height: 700px
                background-repeat: none
                background-size: cover
                cursor: pointer
                display: flex
                justify-content: center
                vertical-align: middle
                align-items: center
                
                @media (max-width: 912px)
                    min-height: 600px
                @media (max-width: 724px)
                    min-height: 450px
                @media (max-width: 550px)
                    min-height: 500px
                .category_button
                    width: 180px
                    height: 70px
                    display: flex
                    justify-content: center
                    border: 1px solid #e0e1e0
                    @media screen and (max-width: 525px)
                    
                    p 
                        color: white
                        text-transform: uppercase
                        display: flex
                        align-items: center
                        letter-spacing: 3px
                        text-align: center
                        text-decoration: none
                        @media screen and (max-width: 525px)
                            font-size: 12px
                        span
                            text-decoration: none


            
</style>