<script>
import store from '@/store'
import axios from 'axios'
 /* eslint-disable */


    export default {
        data() {
            return {
                error: false,
                valid: false,
                payment: {
                    pay: store.state.user.pay,
                    comment: store.state.comment,
                    ifCardTry: false,
                    feedback: store.state.user.feedback,
                    select_feedback: store.state.user.feedbackMethod,
                }
            }
        },
        computed: {
            cartVuex() {
                return this.$store.getters.cart
            },
            button_visible() {
                return (
                    this.$store.state.user.firstName &&
                    this.$store.state.user.firstName.length > 2 &&
                    this.$store.state.user.firstName.length <= 25 &&
                    this.$store.state.user.lastName &&
                    this.$store.state.user.lastName.length > 2 &&
                    this.$store.state.user.lastName.length <= 25 &&
                    this.$store.state.user.number &&
                    this.$store.state.user.number.length >= 9 &&
                    this.$store.state.user.email &&
                    this.$store.state.user.email.length > 4 &&
                    this.$store.state.user.email.includes('@') &&
                    this.$store.state.user.city &&
                    this.$store.state.user.city.length > 1 &&
                    (this.$store.state.user.delivery === 'np' ? (this.$store.state.user.depart && this.$store.state.user.depart.length > 1) : true) &&
                    (this.$store.state.user.delivery === 'cour' ? (this.$store.state.user.street && this.$store.state.user.street.length > 1) : true)
                );
            }
        },
        watch: {
            
        },
        methods: {
            confirm() {

                this.post()
            },
            post() {
                axios.get(`${this.$store.state.url}/csrf_token/`)
                    .then(response => {
                    axios.defaults.xsrfCookieName = 'csrftoken';
                    axios.defaults.headers.common['X-CSRFToken'] = response.data.csrf_token;
                    axios.defaults.xsrfHeaderName = 'X-CSRFToken';

                    let cart = [];
                    for (let x of store.state.cart) {
                        cart.push(x.article + ' (' + x.total + ')');
                    }

                    const orderData = {
                        user: store.state.user,
                        articles: cart
                    };

                    axios.post(`${this.$store.state.url}/submit_order/`, orderData, { withCredentials: true })
                        .then(response => {
                        const orderNumber = response.data.data.orderNumber;
                        console.log('Номер заказа:', orderNumber);
                        if (orderNumber.length === 14) {
                            this.$router.push({ path: `/order/${orderNumber}/` });
                        }
                        })
                        .catch(error => {
                        console.log(error);
                        });
                    });
                },
        },
    }
</script>

<template>
    <div class="checkOutPay">
        <form  class="formConfirm">
            <p class="h3" :class="{darkSimbol : this.$store.state.darkStyle}">Оплата та підтвердження</p>
            <div class="row pay">
                <label>
                    <input @click="payment.ifCardTry = false" type="radio" name="payment" v-model="this.$store.state.user.pay" value="cash" checked>
                    <span>Накладений платіж</span>
                </label>
                <label>
                    <input type="radio" name="payment" v-model="this.$store.state.user.pay" value="card">
                    <span @click="payment.ifCardTry = true"> Карткою (у розробці)</span>
                </label>
            </div>
            <div class=" row comment">
                <transition name="selectAnim2"><p v-if="payment.ifCardTry"
                    mode="out-in"
                    :class="{darkSimbol : this.$store.state.darkStyle}"
                    >Ви все ж таки хочете заплатити онлайн? Тоді після оформлення замовлення ви можете зв'язатися з нами для отримання реквізитів </p></transition>
                <textarea placeholder="Тут ви можете залишити коментар до замовлення" id="comment" name="comment" rows="5" maxlength="100" v-model="this.$store.state.comment"></textarea>
            </div>
            <div class="feedback">
                <label>
                    <input type="checkbox" name="feedback" v-model="this.$store.state.user.feedback" :value="this.$store.state.user.feedback" @change="[payment.feedback = $event.target.checked ? true : false]">
                    <span>Зв'язатися з вами?</span>
                </label>
            <transition name="selectAnim">
                <div v-if="this.$store.state.user.feedback"  class="selectFeedback">
                    <label>
                         <input type="radio" name="selectFeedback" v-model="this.$store.state.user.feedbackMethod" value="tel" checked>
                         <span>Телефоном</span>
                     </label>
                     <label>
                         <input type="radio" name="selectFeedback" value="viber" v-model="this.$store.state.user.feedbackMethod">
                         <span>Viber</span>
                     </label>

                     <label>
                         <input type="radio" name="selectFeedback" value="telegram" v-model="this.$store.state.user.feedbackMethod">
                         <span>Telegram</span>
                     </label>
                </div>
            </transition>
            </div>
            <div class="button-confirm">
                <transition name="button_confirm">
                    <button type="button" @click="confirm" class="button"
                        v-if="cartVuex.length > 0 && button_visible"
                        :class="[
                        this.$store.state.darkVirgin ? {
                            dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
                                } : {
                            darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
                            }
                        ]"
                        >
                        <span :class="{darkSimbol : this.$store.state.darkStyle}"> Оформити замовлення</span>
                    </button>
                </transition>
            </div>
            <div class="error_confirm" v-if="false">
                
            </div>
        </form>
    </div>
</template>

<style lang="sass" scoped>
$primary-color: #00005c
$text-color: mix(#000, $primary-color, 64%)

.checkOutPay
    width: 100%
    .formConfirm
        display: flex
        flex-direction: column
        justify-content: center
        .h3
            display: flex
            justify-content: center
            font-size: 14px
            color: #131621
            text-transform: uppercase
            font-family: FuturaBook
            letter-spacing: 2px
            margin-bottom: 10px
        .pay
            display: grid
            grid-template-columns: 1fr 1fr
            grid-column-gap: 15px
            label
                height: 50px
                font-size: 13px
                letter-spacing: 1px
                display: flex
                cursor: pointer
                position: relative
                overflow: hidden
                input
                    cursor: pointer
                    visibility: hidden
                    position: absolute
                    
                    &:checked + span
                        background-color: mix(#fff, $primary-color, 84%)
                        &:before
                            box-shadow: inset 0 0 0 0.4375em $primary-color
                span
                    background-color: white
                    display: flex
                    align-items: center
                    position: relative
                    top: 1px
                    letter-spacing: 2px
                    color: #131621
                    font-size: 11px
                    padding: 0.375em 0.75em 0.375em 0.375em
                    border-radius: 5px
                    width: 100%
                    transition: 0.25s ease
                    border: 1px solid #e0e1e0
                    padding-left: 10px
                    &:hover
                        background-color: mix(#fff, $primary-color, 84%)
                    &:before
                        display: flex
                        flex-shrink: 0
                        content: ""
                        background-color: #fff
                        width: 1.5em
                        height: 1.5em
                        border-radius: 50%
                        margin-right: 0.375em
                        transition: 0.25s ease
                        box-shadow: inset 0 0 0 0.125em $primary-color
                        margin-right: 7px
        .row
            margin-bottom: 10px
        .comment
            width: 100%
            display: flex
            flex-direction: column
            p
                font-size: 12px
                letter-spacing: 2px
                margin-bottom: 10px
                padding-left: 10px
            textarea
                height: 75px
                width: 100%
                border-radius: 5px !important
                padding-left: 13px
                color: #131621
                font-size: 13px
                letter-spacing: 2px
                border: 1px solid #e0e1e0
                padding: 7px 15px
                &:focus-visible
                    border: 1px solid grey
                    outline-color: grey
        .feedback
            display: grid
            grid-template-columns: 1fr 1fr
            grid-column-gap: 15px
            label
                display: flex
                width: 100%
                height: 50px
                border-radius: 5px !important
                color: #131621
                font-size: 12px
                letter-spacing: 2px
                cursor: pointer
                align-items: center
                input
                    cursor: pointer
                    visibility: hidden
                    position: absolute
                    &:checked + span
                        background-color: mix(#fff, $primary-color, 84%)
                        &:before
                            box-shadow: inset 0 0 0 0.4375em $primary-color
                span
                    display: flex
                    align-items: center
                    position: relative
                    top: 1px
                    height: 100%
                    letter-spacing: 2px
                    color: #131621
                    font-size: 11px
                    padding: 0.375em 0.75em 0.375em 0.375em
                    border-radius: 5px
                    width: 100%
                    transition: 0.25s ease
                    border: 1px solid #e0e1e0
                    background-color: white
                    &:hover
                        background-color: mix(#fff, $primary-color, 84%)
                    &:before
                        display: flex
                        flex-shrink: 0
                        content: ""
                        background-color: #fff
                        width: 1.5em
                        height: 1.5em
                        border-radius: 50%
                        margin-right: 0.375em
                        transition: 0.25s ease
                        box-shadow: inset 0 0 0 0.125em $primary-color
                        margin-left: 7px
            .selectFeedback
                label
                    margin-bottom: 10px

        .button-confirm
            width: 100%
            margin-top: 15px
            .button
                height: 50px
                border: 1px solid #e0e1e0
                transition: 0.25s ease
                width: 100%
                cursor: pointer
                border-radius: 5px
                letter-spacing: 2px
                font-size: 13px
                &:hover
                    background-color: mix(#fff, $primary-color, 84%) !important
        .error_confirm 
            width: 100%
            height: 50px
            border: 1px solid #e0e1e0
            border-radius: 5px
            display: flex
            margin-top: 15px
            align-items: center
            justify-content: center
            letter-spacing: 2px
            font-size: 12px

.selectAnim-enter-active
    transition: all 0.3s
.selectAnim-enter-from, .selectAnim-leave-to
    height: 0px
    opacity: 0
.selectAnim-enter-to, .selectAnim-leave-from
    height: 180px
    opacity: 0.9

.selectAnim2-enter-active
    transition: all 0.5s 
.selectAnim2-enter-from, .selectAnim2-leave-to
    height: 0px
    opacity: 0
.selectAnim2-enter-to, .selectAnim-leave-from
    height: 40px
    opacity: 1

.button_confirm-enter-active, .button_confirm-leave-active
    transition: all 0.7s

.button_confirm-enter-from
    opacity: 0
.button_confirm-enter-to
    opacity: 1

</style>