<template>
  <div class="productSlider">
    <swiper class="swiper"
    :pagination="pagination" :modules="modules"
    >
      <swiper-slide
      class="swiper-slide"
      v-for="(image, index) in images" :key="index"
      >
      <img @error="handleImageError" :src="image" alt="">
    </swiper-slide>
    <swiper-pagination :pagination="{ clickable: true }" class="pagBar">
        <template #pagination>
          
        </template>
      </swiper-pagination>
    </swiper>
  </div>
</template>

<script>
 /*eslint-disable */
 import { Navigation, Pagination, Scrollbar } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

export default {
    components: {
      Swiper,
      SwiperSlide,
    },
    props: {
      images: {
        type: Array,
        required: true,
      },
    data() {
       return {
        pagStyle: {
          width: '50px',
          height: '100px',
          borderRadius: '0px',
          },
        }
      },  
      methods: {
        handleImageError(event) {
            event.target.src = url('error_img.png');
        },
      },
    },
setup(props) {
  return {
    el: '.swiper-pagination',
    bulletClass: 'my-bullet-class',
    bulletActiveClass: 'my-bullet-active-class',
    loop: true,
    pagination: {
      clickable: true,
      renderBullet: function (index, className) {
        if (window.innerWidth > 700 ) {
        return `<div class="${className}" style="background-image:url(${props.images[index]}); width:100px; height: 100px;
        background-size:cover; border-radius: 0px;
        "></div>`; 
      }
        if (window.innerWidth <= 700 * 2) {
          return `<div class="${className}" style="background-image:url(${props.images[index]}); width:50px; height: 50px;
          background-size:cover; border-radius: 0px;
          "></div>`; 
        }
      },
    },
    modules: [Pagination],
  };
},

}
</script>

<style lang="sass" scoped>
.productSlider
  width: 700px
  @media (max-width: 1200px) 
    width: 600px
  @media (max-width: 1050px)
    width: auto
  .swiper
    width: 100%
    height: auto
    display: flex
    flex-direction: column
    .swiper-slide
      text-align: center
      font-size: 18px
      background: #fff
      display: flex
      justify-content: center
      align-items: center
      img
        height: 100%
        width: 100%
        aspect-ratio: 0.9
        cursor: grab
.pagBar
  display: none


</style>