<template>
  <div     
  :class="[
      this.$store.state.darkVirgin ? {
        dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
      } : {
        darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
      }
    ]" class="filter">
    <div class="filterBar">
      <div class="filterBarCenter">
        <p :class="{darkSimbol : this.$store.state.darkStyle}">ФІЛЬТР</p>
        <div @click="filterOff" class="cross"></div>
      </div>
    </div>
    <div class="filterContent">
      <p v-show="$store.state.filter.error">{{$store.state.filter.errorText}}</p>
    <h3 v-if="allColors.length > 0" :class="{darkSimbol : this.$store.state.darkStyle}">Колір</h3>
      <div v-if="allColors.length > 0" class="filterColors">
        <div
        v-for="color in allColors" :key="color" class="filterColor">
          <div @click="[colorSelectFunc(color), colorSelected(color)]" v-show="color[1] == 2 || color[1] == 0 || color[1] == null"  class="color">
            <div v-show="!color[2]" :style="{ backgroundColor: color[0], }" class="colorCubeFalse"></div>
            <div v-show="color[2]" :style="{ backgroundColor: color[0], }" class="colorCubeTrue"></div>
          </div>
          <div  v-show="color[1] == 1" :style="{ backgroundColor: color[0]}" class="color">
            <div v-show="color[0] != 'black' && color[1] == 1" class="colorCross"></div> <div class="whiteColorCross" v-show="color[0] == 'black' && color[1] == 1"></div>
            <!--color[1]="0"-обычное поведение фильтра.="1"-перечеркнуто, фильтр не совпал.="2"=кнопка фильтра активна, совпадает с выбранными филтрами-->
          </div>
        </div>
      </div>
    <h3 v-if="allBrands.length > 0" :class="{ darkSimbol : this.$store.state.darkStyle}">Бренди</h3>
      <div v-if="allBrands.length > 0" class="defaultFilters">
        <div 
        v-for="brand in allBrands" :key="brand"
        class="defaultFilter">
          
          <div class="itemFilter" v-show="brand[1] == 2 || brand[1] == 0 || brand[1] == null" @click="[brandSelectFunc(brand), brandSelected(brand)]">
            <transition name="selected"><div v-show="brand[2]" class="selected" :class="{whiteBackground : this.$store.state.darkStyle}"></div></transition>
            <span :class="{darkSimbol : this.$store.state.darkStyle}">{{brand[0]}}</span>
          </div>
          <div v-show="brand[1] == 1" class="noSuitable itemFilter"><span :class="{darkOpacity : this.$store.state.darkStyle}">{{brand[0]}}</span></div>
        </div>
      </div>
    <h3 v-if="allSizes.length > 0" :class="{darkSimbol : this.$store.state.darkStyle}">Загальний розмір</h3>
      <div v-if="allSizes.length > 0" class="defaultFilters">
        <div 
          v-for="size in allSizes" :key="size"
          class="defaultFilter">
          <div class="itemFilter" v-show="size[1] == 2 || size[1] == 0 || size[1] == null" @click="[sizeSelectFunc(size), sizeSelected(size)]">
            <transition name="selected"><div v-show="size[2]" class="selected" :class="{whiteBackground : this.$store.state.darkStyle}"></div></transition>
            <span :class="{ darkSimbol : this.$store.state.darkStyle}">{{size[0]}}</span>
          </div>
          <div v-show="size[1] == 1" class="noSuitable itemFilter"><span :class="{darkOpacity : this.$store.state.darkStyle}">{{size[0]}}</span></div>
        </div>
      </div>
      
      <h3 v-if="allMaterial.length > 0" :class="{ darkSimbol : this.$store.state.darkStyle}">Матеріал</h3>
        <div v-if="allMaterial.length > 0" class="defaultFilters">
          <div v-for="material in allMaterial" :key="material" class="defaultFilter">
            
            <div class="itemFilter" v-show="material[1] == 2 || material[1] == 0 || material[1] == null" @click="[materialSelectFunc(material), materialSelected(material)]">
              <transition name="selected"><div v-show="material[2]" class="selected" :class="{whiteBackground : this.$store.state.darkStyle}"></div></transition>
              <span :class="{darkSimbol : this.$store.state.darkStyle}">{{material[0]}}</span>
            </div>
            <div v-show="material[1] == 1" class="noSuitable itemFilter"><span :class="{darkOpacity : this.$store.state.darkStyle}">{{material[0]}}</span></div>
          </div>
        </div>

        <h3 v-if="allFiller.length > 0" :class="{darkSimbol : this.$store.state.darkStyle}">Наповнювач</h3>
          <div v-if="allFiller.length > 0" class="defaultFilters">
            <div v-for="filler in allFiller" :key="filler" class="defaultFilter">
              <div class="itemFilter" v-show="filler[1] == 2 || filler[1] == 0 || filler[1] == null" @click="[fillerSelectFunc(filler), fillerSelected(filler)]">
                <transition name="selected">
                  <div v-show="filler[2]" class="selected" :class="{whiteBackground : this.$store.state.darkStyle}"></div>
                </transition>
                <span :class="{darkSimbol : this.$store.state.darkStyle}">{{filler[0]}}</span>
              </div>
              <div v-show="filler[1] == 1" class="noSuitable itemFilter"><span :class="{darkOpacity : this.$store.state.darkStyle}">{{filler[0]}}</span></div>
            </div>
          </div>

          <h3 v-if="allRobe.length > 0" :class="{darkSimbol : this.$store.state.darkStyle}">Розмір одягу</h3>
            <div v-if="allRobe.length > 0" class="defaultFilters">
              <div v-for="robe in allRobe" :key="robe" class="defaultFilter">
                <div class="itemFilter" v-show="robe[1] == 2 || robe[1] == 0 || robe[1] == null" @click="[robeSelectFunc(robe), robeSelected(robe)]">
                  <transition name="selected">
                    <div v-show="robe[2]" class="selected" :class="{whiteBackground : this.$store.state.darkStyle}"></div>
                  </transition>
                  <span :class="{ darkSimbol : this.$store.state.darkStyle}">{{robe[0]}}</span>
                </div>
                <div v-show="robe[1] == 1" class="noSuitable itemFilter"><span :class="{darkOpacity : this.$store.state.darkStyle}">{{robe[0]}}</span></div>
              </div>
            </div>

          <h3 v-if="allRobeCategory.length > 0" :class="{darkSimbol : this.$store.state.darkStyle}">Категорія одягу</h3>
            <div v-if="allRobeCategory.length > 0" class="defaultFilters">
              <div v-for="robe_category in allRobeCategory" :key="robe_category" class="defaultFilter">
                <div class="itemFilter" v-show="robe_category[1] == 2 || robe_category[1] == 0 || robe_category[1] == null" @click="[robeCategorySelectFunc(robe_category), robeCategorySelected(robe_category)]">
                  <transition name="selected">
                    <div v-show="robe_category[2]" class="selected" :class="{whiteBackground : this.$store.state.darkStyle}"></div>
                  </transition>
                  <span :class="{darkSimbol : this.$store.state.darkStyle}">{{robe_category[0]}}</span>
                </div>
                <div v-show="robe_category[1] == 1" class="noSuitable itemFilter"><span :class="{darkOpacity : this.$store.state.darkStyle}">{{robe_category[0]}}</span></div>
              </div>
            </div>

          <h3 v-if="allRug.length > 0" :class="{darkSimbol : this.$store.state.darkStyle}">Розмір килимка</h3>
            <div v-if="allRug.length > 0" class="defaultFilters">
              <div v-for="rug in allRug" :key="rug" class="defaultFilter">
                <div class="itemFilter" v-show="rug[1] == 2 || rug[1] == 0 || rug[1] == null" @click="[rugSelectFunc(rug), rugSelected(rug)]">
                  <transition name="selected">
                    <div v-show="rug[2]" class="selected" :class="{whiteBackground : this.$store.state.darkStyle}"></div>
                  </transition>
                  <span :class="{ darkSimbol : this.$store.state.darkStyle}">{{rug[0]}}</span>
                </div>
                <div v-show="rug[1] == 1" class="noSuitable itemFilter"><span :class="{darkOpacity : this.$store.state.darkStyle}">{{rug[0]}}</span></div>
              </div>
            </div>
            
          <h3 v-if="allTowel.length > 0" :class="{darkSimbol : this.$store.state.darkStyle}">Розмір рушника </h3>
            <div v-if="allTowel.length > 0" class="defaultFilters">
              <div v-for="towel in allTowel" :key="towel" class="defaultFilter">
                <div class="itemFilter" v-show="towel[1] == 2 || towel[1] == 0 || towel[1] == null" @click="[towelSelectFunc(towel), towelSelected(towel)]">
                  <transition name="selected">
                    <div v-show="towel[2]" class="selected" :class="{whiteBackground : this.$store.state.darkStyle}"></div>
                  </transition>
                  <span :class="{darkSimbol : this.$store.state.darkStyle}">{{towel[0]}}</span>
                </div>
                <div v-show="towel[1] == 1" class="noSuitable itemFilter"><span :class="{darkOpacity : this.$store.state.darkStyle}">{{towel[0]}}</span></div>
              </div>
            </div>

          <h3 v-if="allPillowCase.length > 0" :class="{darkSimbol : this.$store.state.darkStyle}">Наволочка</h3>
          <div v-if="allPillowCase.length > 0" class="defaultFilters">
            <div v-for="pillow_case in allPillowCase" :key="pillow_case" class="defaultFilter">
              <div class="itemFilter" v-show="pillow_case[1] == 2 || pillow_case[1] == 0 || pillow_case[1] == null" @click="[pillowCaseSelectFunc(pillow_case), pillowCaseSelected(pillow_case)]">
                <transition name="selected">
                  <div v-show="pillow_case[2]" class="selected" :class="{whiteBackground : this.$store.state.darkStyle}"></div>
                </transition>
                <span :class="{darkSimbol : this.$store.state.darkStyle}">{{pillow_case[0]}}</span>
              </div>
              <div v-show="pillow_case[1] == 1" class="noSuitable itemFilter"><span :class="{darkOpacity : this.$store.state.darkStyle}">{{pillow_case[0]}}</span></div>
            </div>
          </div>

          <h3 v-if="allDuvetSize.length > 0" :class="{darkSimbol : this.$store.state.darkStyle}">Ковдра</h3>
            <div v-if="allDuvetSize.length > 0" class="defaultFilters">
              <div v-for="duvet_size in allDuvetSize" :key="duvet_size" class="defaultFilter">
                <div class="itemFilter" v-show="duvet_size[1] == 2 || duvet_size[1] == 0 || duvet_size[1] == null" @click="[duvetSizeSelectFunc(duvet_size), duvetSizeSelected(duvet_size)]">
                  <transition name="selected">
                    <div v-show="duvet_size[2]" class="selected" :class="{whiteBackground : this.$store.state.darkStyle}"></div>
                  </transition>
                  <span :class="{ darkSimbol : this.$store.state.darkStyle}">{{duvet_size[0]}}</span>
                </div>
                <div v-show="duvet_size[1] == 1" class="noSuitable itemFilter"><span :class="{darkOpacity : this.$store.state.darkStyle}">{{duvet_size[0]}}</span></div>
              </div>
            </div>

          <h3 v-if="allSheetSize.length > 0" :class="{  darkSimbol : this.$store.state.darkStyle }">Простигни</h3>
            <div v-if="allSheetSize.length > 0" class="defaultFilters">
              <div v-for="sheet_size in allSheetSize" :key="sheet_size" class="defaultFilter">
                <div class="itemFilter" v-show="sheet_size[1] == 2 || sheet_size[1] == 0 || sheet_size[1] == null" @click="[sheetSizeSelectFunc(sheet_size), sheetSizeSelected(sheet_size)]">
                  <transition name="selected">
                    <div v-show="sheet_size[2]" class="selected" :class="{ whiteBackground: this.$store.state.darkStyle }"></div>
                  </transition>
                  <span :class="{  darkSimbol : this.$store.state.darkStyle}">{{ sheet_size[0] }}</span>
                </div>
                <div v-show="sheet_size[1] == 1" class="noSuitable itemFilter"><span :class="{ darkOpacity: this.$store.state.darkStyle }">{{ sheet_size[0] }}</span></div>
              </div>
            </div>

      <br>
      <button @click="refreshFilter" class="buttonRefresh"    
       :class="[
          this.$store.state.darkVirgin ? {
            dark : this.$store.state.darkStyle,white : !this.$store.state.darkStyle
          } : {
            darkDefault : this.$store.state.darkStyle , whiteDefault : !this.$store.state.darkStyle
          }
        ]"
    >
        <span :class="{darkSimbol : this.$store.state.darkStyle}">скинути</span>
      </button>
      <template>
        {{$store.state.filter.color}}
        {{$store.state.filter.brand}}
        {{$store.state.filter.size}}
        {{$store.state.filter.material}}
        {{$store.state.filter.filler}}
        {{$store.state.filter.rug}}
        {{$store.state.filter.robe}}
        {{$store.state.filter.towel}}
        {{ $store.state.filter.pillow_case }}
        {{ $store.state.filter.duvet_size }}
        {{ $store.state.filter.sheet_size }}
        {{ $store.state.filter.robe_category }}
      </template>
    </div>
  </div>
</template>

<script>
/*eslint-disable */
import store from '@/store'

export default {
  created() {
    store.commit("VIRGIN")
  },
  mounted() {
    
  },
  data() {
    return {
      test: '',
      colorSelect: [],
      brandSelect: [],
      sizedSelect: [],
      materialSelect: [],
      fillerSelect: [],
      rugSelect: [],
      robeSelect: [],
      robeCategorySelect: [],
      towelSelect: [],
      pillowCaseSelect: [],
      duvetSizeSelect: [],
      sheetSizeSelect: [],
      
      colors: [],
      refresh: "@/assets/img/refresh.png",
      filter: {
        color: [],
        brand: [],
        size: [],
        material: [],
        filler: [],
        rug: [],
        robe: [],
        robe_category: [],
        towel: [],
        pillow_case: [],
        duvet_size: [],
        sheet_size: [],
      }
    }
  },
  props: {
    
  },
  computed: {
    allColors() { //Добавляем из store список цветов товаров 
      let productColors = [] //Сюда
      for(let i in this.$store.state.productsDB) { //У allColors[0] - название самого цвета , [1]- от "0" до "2" ,где 0 и 2 это допуск кнопки к нажатию , 1 - не допуск, [2] true или false ,Нажатая кнопка самом пользвателем или нет
        if(productColors.find(f => this.$store.state.productsDB[i].color == f[0])) { continue } //Если уже такой цвет есть - пропуск
        else if (this.$store.state.productsDB[i].color != null) {  productColors.push([this.$store.state.productsDB[i].color, this.$store.state.productsDB[i].filtered, false]) } //Добавляем цвет 
      } 
       return productColors
    },
    allBrands() {  //Добавляем существующие бренды 
      let productBrands = []
      for(let i in this.$store.state.productsDB) { //У allBrands[0] - название самого бренда , [1]- от "0" до "2" ,где 0 и 2 это допуск кнопки к нажатию , 1 - не допуск ,[2] true или false ,Нажатая кнопка самом пользвателем или нет
        if(productBrands.find(f => this.$store.state.productsDB[i].brand == f[0])) { continue } //Если уже такой есть - пропуск
        else if (this.$store.state.productsDB[i].brand != null) { productBrands.push([this.$store.state.productsDB[i].brand, this.$store.state.productsDB[i].filtered, false]) } //Добавляем бренд 
      } return productBrands
    },
    allSizes() {  //Добавляем существующие размеры
      let productSizes = []
      for(let i in this.$store.state.productsDB) {//У allColors[0] - название самого размера , [1]- от "0" до "2" ,где 0 и 2 это допуск кнопки к нажатию , 1 - не допуск, [2] true или false ,Нажатая кнопка самом пользвателем или нет
        if(productSizes.find(f => this.$store.state.productsDB[i].size == f[0])) { continue } //Если уже такой есть - пропуск
        else if (this.$store.state.productsDB[i].size != null) { productSizes.push([this.$store.state.productsDB[i].size, this.$store.state.productsDB[i].filtered, false])} //Добавляем размер 
      } return productSizes
    },
    allMaterial() {  
      let productMaterials = []
      for(let i in this.$store.state.productsDB) {
        if(productMaterials.find(f => this.$store.state.productsDB[i].material == f[0])) { continue } 
        else if (this.$store.state.productsDB[i].material != null) { productMaterials.push([this.$store.state.productsDB[i].material, this.$store.state.productsDB[i].filtered, false])} 
      } return productMaterials
    },
    allFiller() {  
      let productFiller = []
      for(let i in this.$store.state.productsDB) {
        if(productFiller.find(f => this.$store.state.productsDB[i].filler == f[0])) { continue } 
        else if (this.$store.state.productsDB[i].filler != null) { productFiller.push([this.$store.state.productsDB[i].filler, this.$store.state.productsDB[i].filtered, false])} 
      } return productFiller
    },
    allRug() {  
      let productRug = []
      for(let i in this.$store.state.productsDB) {
        if(productRug.find(f => this.$store.state.productsDB[i].rug == f[0])) { continue } 
        else if (this.$store.state.productsDB[i].rug != null) { productRug.push([this.$store.state.productsDB[i].rug, this.$store.state.productsDB[i].filtered, false])} 
      } return productRug
    },
    allRobe() {  
      let productRobe = []
      for(let i in this.$store.state.productsDB) {
        if(productRobe.find(f => this.$store.state.productsDB[i].robe == f[0])) { continue } 
        else if (this.$store.state.productsDB[i].robe != null) { productRobe.push([this.$store.state.productsDB[i].robe, this.$store.state.productsDB[i].filtered, false])} 
      } return productRobe
    },
    allRobeCategory() {  
      let productRobeCategory = []
      for(let i in this.$store.state.productsDB) {
        if(productRobeCategory.find(f => this.$store.state.productsDB[i].robe_category == f[0])) { continue } 
        else if (this.$store.state.productsDB[i].robe_category != null) { productRobeCategory.push([this.$store.state.productsDB[i].robe_category, this.$store.state.productsDB[i].filtered, false])} 
      } return productRobeCategory
    },
    allTowel() {  
      let productTowel = []
      for(let i in this.$store.state.productsDB) {
        if(productTowel.find(f => this.$store.state.productsDB[i].towel == f[0])) { continue } 
        else if (this.$store.state.productsDB[i].towel != null) { productTowel.push([this.$store.state.productsDB[i].towel, this.$store.state.productsDB[i].filtered, false])} 
      } return productTowel
    },
    allPillowCase() {  
      let productPillowCase = []
      for(let i in this.$store.state.productsDB) {
        if(productPillowCase.find(f => this.$store.state.productsDB[i].pillow_case == f[0])) { continue } 
        else if (this.$store.state.productsDB[i].pillow_case != null) { productPillowCase.push([this.$store.state.productsDB[i].pillow_case, this.$store.state.productsDB[i].filtered, false])} 
        } 
          return productPillowCase
        },

    allDuvetSize() {  
      let productDuvetSize = []
      for(let i in this.$store.state.productsDB) {
        if(productDuvetSize.find(f => this.$store.state.productsDB[i].duvet_size == f[0])) { continue } 
        else if (this.$store.state.productsDB[i].duvet_size != null) { productDuvetSize.push([this.$store.state.productsDB[i].duvet_size, this.$store.state.productsDB[i].filtered, false])} 
      } 
      return productDuvetSize
    },

    allSheetSize() {  
      let productSheetSize = []
      for(let i in this.$store.state.productsDB) {
        if(productSheetSize.find(f => this.$store.state.productsDB[i].sheet_size == f[0])) { continue } 
        else if (this.$store.state.productsDB[i].sheet_size != null) { productSheetSize.push([this.$store.state.productsDB[i].sheet_size, this.$store.state.productsDB[i].filtered, false])} 
      } 
      return productSheetSize
    }
},
  methods: { //Первый индекс в color,brand,size - аналогично: цвет,бренд и размер, во втором индексе - "0"-дефолтное отображение товаров,"1" - товар не совпадает в филтрах(кнопка фильтра серая),"2"- товар совпадает(кнопка фильтра активна)
    colorSelectFunc(color) { //Выбранные цвета в фильтре
      if(this.colorSelect.find(f => f == color[0])) {  
        this.colorSelect.splice(this.colorSelect.indexOf(this.colorSelect.find(f => f == color[0])), 1)  //Удаляем повторно нажатый цвет
      } else { return this.colorSelect.push(color[0]) } //Добавялем нажатый цвет this.colorSelect.push(color)
    },
    colorSelected(color) {
      color[2] = !color[2]
    },
    brandSelectFunc(brand) { 
      if(this.brandSelect.find(f => f == brand[0])) { 
        this.brandSelect.splice(this.brandSelect.indexOf(this.brandSelect.find(f => f == brand[0])), 1)  //Удаляем повторно нажатый бренд
      } else { return this.brandSelect.push(brand[0])  } //Добавялем нажатый бренд      
    },
    brandSelected(brand) {
      brand[2] = !brand[2]//true/false
    },
    sizeSelectFunc(size) { 
      if(this.sizedSelect.find(f => f == size[0])) { 
        this.sizedSelect.splice(this.sizedSelect.indexOf(this.sizedSelect.find(f => f == size[0])), 1)  
      } else {  this.sizedSelect.push(size[0])  }  
    },
    sizeSelected(size) {
      size[2] = !size[2]//true/false
    },
    materialSelectFunc(material) { 
      if(this.materialSelect.find(f => f == material[0])) { 
        this.materialSelect.splice(this.materialSelect.indexOf(this.materialSelect.find(f => f == material[0])), 1)  
      } else { this.materialSelect.push(material[0])  }     
    },
    materialSelected(material) {
      material[2] = !material[2]//true/false
    },
    //
    fillerSelectFunc(filler) { 
      if(this.fillerSelect.find(f => f == filler[0])) { 
        this.fillerSelect.splice(this.fillerSelect.indexOf(this.fillerSelect.find(f => f == filler[0])), 1)  
      } else {  this.fillerSelect.push(filler[0])  }     
    },
    fillerSelected(filler) {
      filler[2] = !filler[2]//true/false
    },
    //
    rugSelectFunc(rug) { 
      if(this.rugSelect.find(f => f == rug[0])) { 
        this.rugSelect.splice(this.rugSelect.indexOf(this.rugSelect.find(f => f == rug[0])), 1)  
      } else {  this.rugSelect.push(rug[0])  }     
    },
    rugSelected(rug) {
      rug[2] = !rug[2]//true/false
    },
    //
    robeSelectFunc(robe) { 
      if(this.robeSelect.find(f => f == robe[0])) { 
        this.robeSelect.splice(this.robeSelect.indexOf(this.robeSelect.find(f => f == robe[0])), 1)  
      } else {  this.robeSelect.push(robe[0])  }     
    },
    robeSelected(robe) {
      robe[2] = !robe[2]//true/false
    },
    //
    robeCategorySelectFunc(robe_category) { 
      if(this.robeCategorySelect.find(f => f == robe_category[0])) { 
        this.robeCategorySelect.splice(this.robeCategorySelect.indexOf(this.robeCategorySelect.find(f => f == robe_category[0])), 1)  
      } else {  this.robeCategorySelect.push(robe_category[0])  }     
    },
    robeCategorySelected(robe_category) {
      robe_category[2] = !robe_category[2]//true/false
    },
    //
    towelSelectFunc(towel) { 
    if(this.towelSelect.find(f => f == towel[0])) { 
      this.towelSelect.splice(this.towelSelect.indexOf(this.towelSelect.find(f => f == towel[0])), 1)  
    } else {  
      this.towelSelect.push(towel[0])  
    }     
    },
    towelSelected(towel) {
      towel[2] = !towel[2] //true/false
    },
    pillowCaseSelectFunc(pillow_case) { 
    if(this.pillowCaseSelect.find(f => f == pillow_case[0])) { 
      this.pillowCaseSelect.splice(this.pillowCaseSelect.indexOf(this.pillowCaseSelect.find(f => f == pillow_case[0])), 1)  
    } else {  
      this.pillowCaseSelect.push(pillow_case[0])  
    }     
    },
    pillowCaseSelected(pillow_case) {
      pillow_case[2] = !pillow_case[2] //true/false
    },

    duvetSizeSelectFunc(duvet_size) { 
      if(this.duvetSizeSelect.find(f => f == duvet_size[0])) { 
