    },
    
    ///////

    filterOff() {
      store.commit("FILTER_SHOW") //Закрываем фильтр
    },
    refreshFilter() { 
      store.commit("FILTER_REFRESH")
      this.colorSelect = [];
      this.brandSelect = [];
      this.sizedSelect = [];
      this.materialSelect = [];
      this.fillerSelect = [];
      this.robeSelect = [];
      this.robeCategorySelect = [];
      this.rugSelect = [];
      this.towelSelect = [];
      this.pillowCaseSelect = [];
      this.duvetSizeSelect = [];
      this.sheetSizeSelect = [];
      
      for(let refresh of this.allColors) {
        refresh[2] = false
      }
      for(let refresh of this.allBrands) {
        refresh[2] = false
      }
      for(let refresh of this.allSizes) {
        refresh[2] = false
      }
      for(let refresh of this.allMaterial) {
        refresh[2] = false
      }
      for(let refresh of this.allFiller) {
        refresh[2] = false
      }
      for(let refresh of this.allRobe) {
        refresh[2] = false
      }
      for(let refresh of this.allRobeCategory) {
        refresh[2] = false
      }
      for(let refresh of this.allRug) {
        refresh[2] = false
      }
      for(let refresh of this.allTowel) {
        refresh[2] = false
      }
      for(let refresh of this.allPillowCase) {
        refresh[2] = false
      }
      for(let refresh of this.allDuvetSize) {
        refresh[2] = false
      }
      for(let refresh of this.allSheetSize) {
        refresh[2] = false
      }
    }
  },
  watch: {
    colorSelect: {
      handler() { //Если юзер жмет фильтр цвета
        store.commit("FILTER_COLOR", this.colorSelect) //Завозим выбранные цвета
        store.commit('FILTER') //и вызываем изменения прайс-листа 
        this.filter.color = this.colorSelect //Для создания общего watch filter ,который ниже
      },
      deep: true,
    },
    brandSelect: {
      handler() { //Если юзер жмет фильтр бренда
        store.commit("FILTER_BRAND", this.brandSelect) //Завозим выбранные бренды
        store.commit('FILTER') //и вызываем изменения прайс-листа 
        this.filter.brand = this.brandSelect //Для создания общего watch filter ,который ниже
