        store.commit("FILTER_DUVET_SIZE", this.duvetSizeSelect) //Завозим выбранные размеры одеяла
        store.commit('FILTER') //и вызываем изменения прайс-листа 
        this.filter.duvet_size = this.duvetSizeSelect //Для создания общего watch filter ,который ниже
      },
      deep: true,
      },
      sheetSizeSelect: {
        handler() { //Если юзер жмет фильтр размера простыни
          store.commit("FILTER_SHEET_SIZE", this.sheetSizeSelect) //Завозим выбранные размеры простыни
          store.commit('FILTER') //и вызываем изменения прайс-листа 
          this.filter.sheet_size = this.sheetSizeSelect //Для создания общего watch filter ,который ниже
        },
        deep: true,
    },
    filter: { //Делаем кнопки некликабельными,если они вызовут пустой прайс-лист 
      handler() { 
        for (let color of this.allColors) { 
          if(!store.state.products.find(f=>f.color == color[0])) { //Делаем серыми кнопки цвета
            color[1] = 1
          }
          else color[1] = 2
        }
        for (let brand of this.allBrands) {
          if(!store.state.products.find(f=>f.brand == brand[0])) { //Делаем серыми кнопки бренда
            brand[1] = 1 
          }
          else brand[1] = 2
        }
        for (let size of this.allSizes) { 
          if(!store.state.products.find(f=>f.size == size[0])) { //Делаем серыми кнопки размера
            size[1] = 1
          }
          else size[1] = 2
        }
        for (let material of this.allMaterial) { 
          if(!store.state.products.find(f=>f.material == material[0])) { //Делаем серыми кнопки материала
            material[1] = 1
          }
          else material[1] = 2
        }
        for (let filler of this.allFiller) { 
          if(!store.state.products.find(f=>f.filler == filler[0])) { //Делаем серыми кнопки наполнителя
            filler[1] = 1
          }
          else filler[1] = 2
        }
        for (let rug of this.allRug) { 
          if(!store.state.products.find(f=>f.rug == rug[0])) { //Делаем серыми кнопки ковра
            rug[1] = 1
          }
          else rug[1] = 2
        }
        for (let robe of this.allRobe) { 
          if(!store.state.products.find(f=>f.robe == robe[0])) { //Делаем серыми кнопки халата
            robe[1] = 1
          }
          else robe[1] = 2
        }
        for (let robe_category of this.allRobeCategory) { 
          if(!store.state.products.find(f=>f.robe_category == robe_category[0])) { //Делаем серыми кнопки халата
            robe_category[1] = 1
          }
          else robe_category[1] = 2
        }
        for (let towel of this.allTowel) { 
          if(!store.state.products.find(f=>f.towel == towel[0])) { //Делаем серыми кнопки полотенца
            towel[1] = 1
          }
          else towel[1] = 2
        }
        for (let pillow_case of this.allPillowCase) { 
          if(!store.state.products.find(f=>f.pillow_case == pillow_case[0])) { //Делаем серыми кнопки наволочки
            pillow_case[1] = 1
          }
          else pillow_case[1] = 2
        }
        for (let duvetSize of this.allDuvetSize) { 
          if(!store.state.products.find(f=>f.duvet_size == duvetSize[0])) { //Делаем серыми кнопки размера одеяла
            duvetSize[1] = 1
          }
          else duvetSize[1] = 2
        }
        for (let sheetSize of this.allSheetSize) { 
          if(!store.state.products.find(f=>f.sheet_size == sheetSize[0])) { //Делаем серыми кнопки размера одеяла
            sheetSize[1] = 1
          }
          else sheetSize[1] = 2
        }
        if(
          this.filter.material.length == 0 && this.filter.brand.length == 0 && this.filter.size.length == 0 &&
          this.filter.filler.length == 0 && this.filter.robe.length == 0 && this.filter.rug.length == 0 &&
          this.filter.towel.length == 0 && this.filter.pillow_case.length == 0 && this.filter.duvet_size.length == 0 &&
          this.filter.sheet_size.length == 0 && this.filter.robe_category.length == 0
        ) {
          for (let clear of this.allMaterial) {clear[1] = 0}
          for (let clear of this.allBrands) {clear[1] = 0}
          for (let clear of this.allSizes) {clear[1] = 0}
          for (let clear of this.allFiller) {clear[1] = 0}
          for (let clear of this.allRobe) {clear[1] = 0}
          for (let clear of this.allRobeCategory) {clear[1] = 0}
          for (let clear of this.allRug) {clear[1] = 0}
          for (let clear of this.allTowel) {clear[1] = 0}
          for (let clear of this.allPillowCase) {clear[1] = 0}
          for (let clear of this.allDuvetSize) {clear[1] = 0}
          for (let clear of this.allSheetSize) {clear[1] = 0}
        }
      },
      deep: true
    },
  },

}
